.home {
    background-color: #0F55A7;
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;

    .grid1 {
        width: 220px;
        background-color: white;
    }

    .heading2 {
        width: 100%;
        margin-top: -40px;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);

        .headCon {
            width: 95%;
            margin: auto;
            height: 60px;
            position: relative;

            .dayBox {
                display: inline-block;
                position: absolute;
                z-index: 2;

                .day {
                    margin-top: 20px;
                    width: 150px;
                    line-height: 26px;
                    background-color: white;
                    border-radius: 5px;
                    text-align: center;
                    font-size: 14px;
                    font-weight: 500;
                    color: #0075FF;
                    position: relative;
                    display: inline-block;
                    cursor: pointer;


                    span {
                        position: absolute;
                        left: 10px;
                        top: -2px;
                    }
                }

                .btnBox {
                    width: 150px;
                    height: auto;
                    padding-bottom: 10px;
                    padding-Top: 5px;
                    background-color: white;
                    margin-top: 5px;
                    border-radius: 5px;
                    font-size: 12px;
                    box-shadow: 1px 1px 10px rgb(211, 210, 210);
                    display: none;


                    .btn1 {
                        margin-top: 5px;
                        height: 35px;
                        width: 90%;
                        margin-left: 7px;
                        color: #0075FF;
                        font-size: 12px;
                        font-weight: 600;
                        text-transform: none;
                        letter-spacing: .5px;
                        border-color: #CFE5FF;
                    }
                }
            }
        }
    }

    .top {
        width: 95%;
        margin: auto;
        margin-top: 16px;
        min-height: 80px;
        display: grid;
        grid-template-columns: 1fr 33px 1fr 33px 1fr 33px 1fr;

        .data1,
        .data2,
        .data3,
        .data4 {
            width: 100%;
            height: 70px;
            background-color: white;
            border-radius: 10px;
        }

        .data1,
        .data2,
        .data3 {
            position: relative;

            .box {
                width: 35px;
                height: 35px;
                background-color: #0075FF;
                position: absolute;
                top: 18px;
                right: 15px;
                border-radius: 5px;
                display: grid;
                place-content: center;
                color: white;
            }

            p {
                font-size: 12px;
                position: absolute;
                top: 10px;
                left: 15px;
                color: #797979;
                font-weight: 500;
            }

            section {
                position: absolute;
                bottom: 10px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            span {
                position: absolute;
                bottom: 13px;
                left: 95px;
                font-size: 10px;
                color: green;
                font-weight: 500;
            }
        }

        .data4 {
            font-weight: 500;
            color: #0075FF;
            font-size: 14px;

            .btt {
                text-transform: none;
                width: 100%;
                height: 100%;
                border-radius: 10px;
                background-color: white;
                font-family: Arial, Helvetica, sans-serif;
                font-size: 18px;

                span {
                    margin-right: 8px;
                    margin-top: -4px;
                }
            }

        }
    }


    .scroll {
        width: 100%;
        // height: 91vh !important;

        height: 94vh !important;

        padding-bottom: 10px;
        max-height: 750px !important;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

    }


    .mid {
        width: 95%;
        margin: auto;
        min-height: 400px;
        display: grid;
        grid-template-columns: 77% 23%;

        .graph {
            width: 96%;
            height: 380px;
            background-color: white;
            margin-top: 10px;
            border-radius: 10px;
            position: relative;
            padding-top: 10px;
            padding-right: 20px;

            .headings {
                position: absolute;
                top: 15px;
                left: 20px;
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
            }

            .per {
                position: absolute;
                top: 50px;
                left: 20px;
                font-size: 14px;
                font-weight: 500;
                color: green;
            }

            .selected {
                margin-left: auto;
                width: 160px;
                position: relative;
                z-index: 10;

                input {
                    background-color: #CFE5FF;
                    border: none;
                    border-radius: 5px;
                    width: 160px;
                    line-height: 25px;
                    font-size: 12px;
                    text-align: end;
                    padding-right: 40px;
                    color: #0F55A7;
                    font-weight: 600;
                    cursor: pointer;
                }

                .icon {
                    position: absolute;
                    right: 10px;
                    color: #0F55A7;
                }

                .conBox {
                    width: 160px;
                    border-radius: 5px;
                    margin-top: 3px;
                    display: none;
                    background-color: white;

                    .but {
                        margin-top: 5px;
                        height: 35px;
                        width: 160px;
                        color: #0075FF;
                        font-size: 12px;
                        font-weight: 600;
                        text-transform: none;
                        letter-spacing: .5px;
                        border-color: #CFE5FF;
                    }
                }
            }

            .chartCon {
                width: 100%;
                position: absolute;
                bottom: 10px;
            }
        }

        .cities {
            width: 100%;
            height: 380px;
            background-color: white;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
            position: relative;
            padding-top: 40px;

            .head {
                position: absolute;
                top: 10px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            .index {
                display: grid;
                width: 90%;
                grid-template-columns: 1fr 1fr;
                font-size: 12px;
                margin: auto;
                color: #0075FF;
            }

            .dataBox {
                width: 90%;
                margin: auto;
                height: 300px;
                margin-top: 10px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }

        }
    }


    .bottom {
        width: 95%;
        margin: auto;
        margin-top: 10px;
        display: grid;
        grid-template-columns: 1fr 1fr;

        .box1,
        .box2 {
            width: 97%;
            margin-top: 10px;
            background-color: white;
            border-radius: 10px;
        }


        .box2 {
            position: relative;
            margin-left: auto;
            height: 680px;

            section {
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
                padding-left: 25px;
                padding-top: 15px;
            }

            .btnAdd {
                position: absolute;
                top: 20px;
                right: 22px;
                width: 120px;
                padding: 2px 30px 2px 30px;
                text-transform: none;
                background-color: #0F55A7;
                color: white;
                font-weight: bold;
            }

            .heads {
                width: 90%;
                margin: auto;
                margin-top: 20px;
                position: relative;

                #inp {
                    position: relative;
                    width: 100% !important;

                    input {
                        border-radius: 5px;
                        line-height: 30px;
                        border: none;
                        box-shadow: 1px 0px 10px rgb(224, 223, 223);
                        width: 200px;
                        font-size: 12px;
                        padding-left: 40px;

                    }

                    .serIcon {
                        position: absolute;
                        left: 10px;
                        z-index: 2;
                        top: 5px;
                        color: #797979;
                    }
                }

            }

            .sorting {
                width: 120px;
                position: absolute;
                right: 22px;
                top: 65px;
                z-index: 2;

                input {
                    width: 120px;
                    border-radius: 5px;
                    line-height: 30px;
                    border: none;
                    background-color: #CFE5FF;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: #0075FF;
                    cursor: pointer;
                    outline: none;
                }

                span {
                    position: absolute;
                    top: 1.5px;
                    right: 10px;
                    color: #0075FF;
                }

                .btnCon1 {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 8px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }

                }
            }


            .head2 {
                width: 90%;
                line-height: 40px;
                margin: auto;
                margin-top: 10px;
                display: grid;
                grid-template-columns: 1fr 80px 122px 40px !important;
                font-weight: 500;
                color: #0F55A7;


                .d {
                    text-align: center;
                }

            }

            .inner {
                width: 90%;
                margin: auto;
                height: 400px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }


        }

        .comBox {
            border-radius: 0px !important;
            background-color: rgb(255, 255, 255, 0) !important;
        }

