.dataArray {
    width: 100%;
    line-height: 50px;
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 80px 122px 40px !important;


    .d {
        color: #797979;
        font-size: 12px;
        text-align: center;

        span {
            color: #0F55A7;
            cursor: pointer !important;
        }
    }

    .d1 {
        font-weight: 500;
        padding-left: 0px !important;
        color: #474747;
        font-size: 16px;
        margin-top: -10px;
        margin-right: 10px;

        .nm {
            max-width: 140px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .addr {
            max-width: 140px;
            font-size: 12px;
            margin-top: -25px;
            color: #797979;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}