.non {
    text-decoration: none;
    color: #6B8BAF;
}

.active {
    .Items {
        background-color: #CFE5FF !important;

        .imgCon {
            background-color: #0075FF !important;
            color: white !important;
        }
    }
}


.sideBar {
    width: 220px;
    height: 100%;
    background-color: white;
    padding-top: 40px;
    color: #0F55A7;
    font-weight: 600;
    position: absolute;
    left: 0px;
    top: 0px;
    overflow-y: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar{
        display: none;
    }

    h2 {
        text-align: center;
        font-family: 'Redressed';
        position: relative;
        margin-bottom: 40px;

        span {
            position: absolute;
            top: 33px;
            font-size: 12px;
            font-family: 'Roboto';
            letter-spacing: 3.7px;
            left: 67px;
        }
    }

    .Items {
        width: 190px;
        line-height: 42px;
        margin: auto;
        margin-top: 8px;
        border-radius: 12px;
        position: relative;
        padding-left: 50px;
        color: #0075FF;
        font-size: 12px;

        .imgCon {
            width: 25px;
            height: 25px;
            background-color: #CFE5FF;
            position: absolute;
            top: 8px;
            left: 10px;
            border-radius: none;
            border-radius: 8px;
            display: grid;
            place-content: center;
        }
    }

    .Items:hover {
        background-color: #CFE5FF;

        .imgCon {
            background-color: #0075FF;
            color: white;
        }
    }

    section {
        margin-top: 20px;
        margin-left: 30px;
        margin-bottom: 15px;
    }

    .opt1 {
        padding-top: 5px;
        margin-bottom: 0px;
        padding-right: 20px;

        li {
            list-style: none;
            font-size: 10px;
            line-height: 35px;
            padding-left: 15px;
            width: 150px;
            margin-left: auto;
            border-bottom: 1px solid rgb(107, 139, 175, 0.2);

        }

        #active1 {
            color: #0F55A7 !important;
            font-weight: 600 !important;
        }
    }

}