.OrderCard{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 70% !important;
    padding: 20px;
    border-top: 1px solid rgb(227, 226, 226);
    cursor: pointer;

    .name{
        padding-left: 0% !important;
        padding-right: 50px;

        .userName{
            color: #5e5d5d;
            font-size: 24px;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: bold !important;
        }

        .city{
            color: #797979;
            font-size: 14px;
            font-weight: 500;
            margin-top: 5px;
        }
        .date{
            color: #797979;
            font-size: 12px;
            margin-top: 5px;
        }
    }

    .itemBox{
        .item{
            width: 100%;
            padding: 10px;
            display: grid;
            grid-template-columns: 1fr 140px 140px !important;
            color: #797979;

            .main{
                display: grid;
                grid-template-columns: 90px 1fr;
                img{
                    width: 60px;
                    height: 60px;
                }
                p{
                    font-size: 12px;
                    margin-bottom: 0px;
                    max-width: 200px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }

            .m{
                 display: grid;
                 place-content: center;
            }
        }
    }
}