.addgatekeeper {
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.4);
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .inner {
        width: 450px;
        height: 500px;
        background-color: white;
        border-radius: 20px;
        padding: 25px;
        padding-top: 15px;
        position: relative;


        .head {
            color: #0F55A7;
            font-weight: bold;
            font-size: 20px;
            letter-spacing: .4px;
        }

        .head2 {
            font-weight: 700;
            margin-top: 25px;
            margin-bottom: -10px;
            letter-spacing: .4px;
            color: #474747;
        }

        .head3 {
            font-weight: 700;
            margin-top: 5px;
            letter-spacing: .4px;
            color: #474747;
        }

        .btn {
            position: absolute;
            top: 10px;
            right: 20px;
            padding: 0px;
            color: #C6C6C6;
        }

        form {

            .d1,
            .d2 {

                .form-group {
                    margin-top: 30px;
                    font-weight: 500;
                    color: #797979;
                    font-size: 14px;

                    input {
                        margin-top: 5px;
                        font-size: 12px;
                        border: 1px solid rgba(0, 0, 0, 0.25);
                        border-radius: 8px;
                        line-height: 25px;
                        letter-spacing: .5px;

                        &::placeholder {
                            color: #C6C6C6;
                        }
                    }

                    .info {
                        font-size: 11px;
                        letter-spacing: .5px;
                        text-align: center;
                        font-style: italic;
                        color: #797979;
                        font-weight: 400;
                    }
                }


            }

            .btnCon {
                text-align: center;
                margin-top: 40px;

                .next {
                    text-transform: none;
                    width: 100px;
                    height: 35px;
                    color: white;
                    font-weight: bold;
                    background-color: #0075FF;
                }
            }
        }


        form {
            .d2 {
                margin-top: 27px;

                .form-group {
                    margin-top: 14px;

                    input {
                        line-height: 22px;
                        border-radius: 6px;
                    }
                }
            }
        }

        form {
            .d3 {
                width: 80%;
                margin: auto;
                height: 320px;
                margin-bottom: -5px;
                position: relative;

                .uppertext {
                    font-size: 10px;
                    font-style: italic;
                    color: #797979;
                    font-weight: 500;
                    margin-top: 20px;
                }

                .inp1 {
                    width: 100%;
                    background-color: #eaf1fc;
                    line-height: 20px;
                    margin-top: 10px;
                    font-size: 11px;
                    font-weight: 500;
                    border-radius: 8px;
                    padding-left: 40px;
                    letter-spacing: .5px;

                    &::placeholder {
                        color: #a4a4a4;
                    }

                }

                .icon1 {
                    position: absolute;
                    top: 33px;
                    left: 10px;
                    font-size: 20px;
                    color: #a4a4a4;
                }

                .icon2 {
                    position: absolute;
                    top: 73px;
                    left: 10px;
                    font-size: 25px;
                    z-index: 2;
                    color: #a4a4a4;
                }

                .inp2 {
                    width: 100%;
                    line-height: 20px;
                    margin-top: 10px;
                    font-size: 11px;
                    font-weight: 500;
                    background-color: white;
                    border: 1px solid rgba(0, 0, 0, 0.25);
                    border-radius: 8px;
                    padding-left: 40px;
                    letter-spacing: .4px;
                    cursor: pointer;

                    &::placeholder {
                        color: #a4a4a4;
                    }

                    box-shadow: none;
                }

                .boxCon {
                    width: 100%;
                    margin-top: 10px;
                    height: 210px;
                    border-radius: 8px;
                    border: 1px solid #CFE5FF;
                    overflow-y: scroll;

                    .productBox {
                        width: 90%;
                        margin: auto;
                        height: 50px;
                        position: relative;
                        margin-top: 5px;
                        margin-bottom: 5px;
                        color: #797979;

                        img {
                            position: absolute;
                            width: 40px;
                            height: 40px;
                            left: 5px;
                            top: 5px;
                        }

                        .name {
                            position: absolute;
                            left: 60px;
                            top: 5px;
                            width: 160px;
                            font-size: 14px;
                            font-weight: 500;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .spcate {
                            position: absolute;
                            width: 170px;
                            left: 60px;
                            font-size: 12px;
                            bottom: 7px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .btn {
                            position: absolute;
                            right: 5px !important;
                            top: 15px;
                            padding: 0px;
                            width: 20px;
                            height: 20px;
                            min-height: 10px;
                            display: grid;
                            place-content: center;
                            color: #797979;
                        }

                    }
                }

                .boxPop {
                    position: absolute;
                    width: 330px;
                    height: 230px;
                    bottom: -20px;
                    left: -5px;
                    border-radius: 8px;
                    background-color: white;
                    box-shadow: 0px 0px 5px #a4a4a4;

                    .top1 {
                        width: 100%;
                        line-height: 40px;
                        border-bottom: 1px solid #D9D9D9;
                        color: #949393;
                        font-weight: 600;
                        font-size: 14px;
                        text-align: right;
                        padding-right: 50px;
                        letter-spacing: .3px;
                        font-family: Arial, Helvetica, sans-serif;

                        input {
                            position: relative;
                            top: 8px;
                            left: 8px;
                            width: 17px;
                            height: 17px;
                            border-color: #0075FF;
                        }
                    }

                    .inner1 {
                        height: 188px;
                        padding-top: 5px;
                        overflow-y: scroll;
                    }

                    .productBox {
                        width: 90%;
                        margin: auto;
                        height: 50px;
                        position: relative;
                        margin-bottom: 5px;
                        color: #797979;

                        img {
                            position: absolute;
                            width: 40px;
                            height: 40px;
                            left: 5px;
                            top: 5px;
                        }

                        .name {
                            position: absolute;
                            left: 60px;
                            top: 5px;
                            width: 160px;
                            font-size: 14px;
                            font-weight: 500;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .spcate {
                            position: absolute;
                            width: 170px;
                            left: 60px;
                            font-size: 12px;
                            bottom: 7px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .form-check-input {
                            position: absolute;
                            right: 9px;
                            top: 9px;
                            width: 17px;
                            height: 17px;
                            border-color: #0075FF;
                        }

                    }
                }

            }

        }


    }



    .inner2 {
        width: 500px;
        height: 350px;
        background-color: white;
        border-radius: 20px;
        padding: 15px;
        position: relative;

        .header4 {
            color: #474747;
            font-weight: 700;
            letter-spacing: .5px;
            padding-left: 50px;
            font-size: 18px;

            .btn1 {
                position: absolute;
                left: 25px;
                top: 23px;
                font-size: 28px;
                padding: 0px;
                height: 15px;
                width: 30px;
                display: grid;
                place-content: center;
                color: #474747;
            }
        }

        .info {
            font-size: 12px;
            color: #797979;
        }

        .btn2 {
            position: absolute;
            top: 8px;
            right: 15px;
            padding: 0px;
            color: #C6C6C6;
        }

        .form-group {
            width: 90%;
            margin: auto;
            margin-top: 40px;
            position: relative;

            input {
                margin-top: 5px;
                border: 1px solid #C6C6C6;
                font-size: 14px;
                border-radius: 8px;
                line-height: 25px;

                &::placeholder {
                    color: #C6C6C6;
                }
            }

            #code {
                position: absolute;
                color: #0075FF;
                right: 15px;
                top: 31px;
                font-size: 14px;
                font-weight: 500;
                letter-spacing: .5px;
                cursor: pointer;
            }

            #time {
                font-size: 10px;
                color: #0075FF;
                position: absolute;
                right: 2px;
                bottom: -18px;
                letter-spacing: .3px;
                display: none;
            }
        }

        .btnBox1 {
            margin-top: 100px;
            text-align: center;

            .bt1,
            .bt2 {
                background-color: #0075FF;
                color: white;
                font-weight: bold;
                text-transform: none;
                width: 100px;
                height: 40px;
            }

            .bt2 {
                background-color: #6B8BAF;
            }
        }

    }



}