.addproduct {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;

    .errors {
        position: absolute;
        color: red;
        font-size: 12px;
        font-style: italic;
    }

    ///////////////////////////////////////////////////
    textarea,
    input {
        text-transform: capitalize;
    }

    //////////////////////////////////////////////////


    .inner {
        width: 95% !important;
        height: 92vh;
        margin: auto;
        margin-top: 15px;
        overflow-y: scroll;
        padding-bottom: 10px;

        &::-webkit-scrollbar {
            display: none;
        }

        .addCon {
            width: 100%;
            background-color: #CFE5FF;
            border-radius: 15px;
            padding: 20px;

            h4 {
                font-weight: 600;
                color: #0F55A7;
                font-family: Arial;
                padding-left: 10px;
                font-size: 21px;
            }

            .first {
                width: 98%;
                height: 400px;
                background-color: white;
                margin: auto;
                margin-top: 30px;
                border-radius: 10px;
                box-shadow: 1px 1px 10px #d2d2d2;

                .heading {
                    height: 60px;
                    padding-top: 15px;
                    padding-left: 30px;
                    font-weight: 500;
                    font-size: 17px;
                    color: #474747;
                    border-bottom: 1px solid #D9D9D9;
                }

                .box {
                    width: 100%;
                    height: 320px;
                    display: grid;
                    grid-template-columns: 1fr 1fr;

                    .b1 {
                        display: grid;
                        place-content: center;
                    }

                    .b2 {
                        display: grid;
                        place-content: center;

                        .bb2 {
                            width: 180px;
                            height: 180px;
                            border-radius: 20px;
                            border: 1px dashed #0075FF;
                            display: grid;
                            place-content: center;

                            div {
                                width: 160px;
                                height: 160px;
                                background-color: #CFE5FF;
                                border-radius: 15px;
                                display: grid;
                                place-content: center;
                                position: relative;

                                img {
                                    width: 70px;
                                    height: 70px;
                                }

                                #file {
                                    position: absolute;
                                    width: 160px;
                                    height: 160px;
                                    border-radius: 15px;
                                    opacity: 0;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
            }

            .second {
                width: 98%;
                margin: auto;
                display: grid;
                grid-template-columns: 1fr 400px;


                .one {

                    .box1,
                    .box3,
                    .box5,
                    .box7 {
                        width: 97%;
                        background-color: white;
                        border-radius: 10px;
                        box-shadow: 1px 1px 10px #d2d2d2;

                        .heading {
                            margin-top: 20px;
                            height: 60px;
                            padding-top: 15px;
                            padding-left: 30px;
                            font-weight: 500;
                            font-size: 17px;
                            color: #474747;
                            border-bottom: 1px solid #D9D9D9;
                        }

                    }

                    .box1 {
                        padding-bottom: 30px;

                        .form-group {
                            width: 90%;
                            margin: auto;
                            margin-top: 25px;
                            position: relative;


                            input,
                            textarea {
                                margin-top: 6px;
                            }

                            label {
                                color: #474747;
                                margin-top: 6px;
                            }
                        }

                        .logo_grid {
                            width: 90%;
                            display: grid;
                            grid-template-columns: 1fr 1fr;
                            margin: auto;

                            .check {
                                input {
                                    width: 20px;
                                    height: 20px;
                                    border-color: #0075FF;
                                }

                                label {
                                    position: relative;
                                    left: 4px;
                                    top: -0.5px;
                                    font-weight: 500;
                                    font-size: 14px;
                                    color: #0075FF;
                                }
                            }

                            .logo_drop {
                                display: grid;
                                align-items: end;
                                justify-content: end;

                                .drop {
                                    border-radius: 6px;
                                    border: 1px solid black;
                                    position: relative;
                                    height: 30px;

                                    .top_div {
                                        width: 180px;
                                        line-height: 30px;
                                        border-radius: 6px;
                                        font-size: 14px;
                                        font-weight: 600;
                                        padding-left: 10px;
                                        cursor: pointer;
                                    }

                                    .opt {
                                        position: absolute;
                                        width: 180px;
                                        height: 125px;
                                        margin-top: 3px;
                                        border: 1px solid black;
                                        border-radius: 6px;
                                        background-color: white;
                                        overflow-y: scroll;

                                        &::-webkit-scrollbar {
                                            display: none;
                                        }
                                    }

                                    .mainlogo {
                                        width: 100%;
                                        display: grid;
                                        grid-template-columns: 1fr 50px;
                                        padding: 5px;
                                        border-bottom: 1px dotted gray;
                                        cursor: pointer;

                                        .titleCon {
                                            display: grid;
                                            font-size: 14px;
                                            font-weight: 600;
                                            align-items: center;
                                        }

                                        .logoCon {
                                            img {
                                                width: 50px;
                                                height: 30px;
                                            }
                                        }
                                    }
                                }
                            }
                        }


                    }


                    .box3 {
                        color: #474747;

                        .form-group {
                            width: 90%;
                            margin: auto;
                            margin-top: 30px;
                        }

                        input {
                            margin-top: 10px;
                        }

                        .foot {
                            display: grid;
                            grid-template-columns: 1fr 2fr;
                            line-height: 70px;
                            margin-top: 20px;

                            .d1 {
                                text-align: center;
                                color: #0075FF;
                                font-weight: 500;
                                font-size: 15px;
                                cursor: pointer;
                            }

                            .d2 {
                                font-size: 12px;
                                color: #797979;
                            }
                        }

                        #addInfo {
                            width: 100%;
                            height: 100vh;
                            position: fixed;
                            top: 0px;
                            left: 0px;
                            background-color: rgb(0, 0, 0, 0.5);
                            display: grid;
                            place-content: center;
                            z-index: 20;

                            .add {
                                width: 700px;
                                height: 420px;
                                background-color: white;
                                border-radius: 15px;
                                position: relative;

                                .head {
                                    color: white;
                                    line-height: 70px;
                                    background-color: #0075FF;
                                    padding-left: 20px;
                                    border-radius: 15px 15px 0px 0px;
                                    font-family: Arial;
                                    letter-spacing: .5px;
                                    position: relative;

                                    .btn {
                                        position: absolute;
                                        top: 5px;
                                        right: 15px;
                                        color: white;
                                    }
                                }

                                .form-group {
                                    width: 90%;
                                    margin: auto;
                                    margin-top: 20px;
                                    font-size: 14px;
                                    color: #474747;

                                    input,
                                    textArea {
                                        margin-top: 10px !important;
                                    }
                                }

                                .bt {
                                    position: absolute;
                                    bottom: 20px;
                                    right: 35px;
                                    background-color: #0075FF;
                                    color: white;
                                    text-transform: none;
                                    font-weight: 600;
                                    width: 80px;
                                }

                                .bt1 {
                                    position: absolute;
                                    bottom: 20px;
                                    right: 135px;
                                    color: #0075FF;
                                    border-color: #0075FF;
                                    text-transform: none;
                                    font-weight: 600;
                                    width: 80px;
                                }
                            }
                        }
                    }

                    .box5 {
                        height: 320px;

                        .section {
                            display: grid;
                            grid-template-columns: 1fr 1fr;

                            .form-group {
                                margin: auto;
                                margin-top: 30px;
                                width: 80%;
                                color: #474747;
                                font-size: 14px;
                                position: relative;

                                label {
                                    margin-bottom: 10px;
                                }

                                input {
                                    border-color: #CFE5FF;
                                    padding-left: 40px;
                                }

                                input[type=checkbox] {
                                    padding-left: 0px;
                                }

                                .s1 {
                                    position: absolute;
                                    left: 15px;
                                    bottom: 4px;
                                    font-size: 20px;
                                    font-weight: 600;
                                    color: #0075FF;
                                }

                                .s2 {
                                    position: absolute;
                                    right: 10px !important;
                                    top: 1px;
                                    font-size: 20px;
                                    font-weight: 600;
                                    color: #0075FF;
                                }

                                .btn {
                                    color: white;
                                    min-width: 4px;
                                    width: 4px;
                                    height: 23px;
                                    display: grid;
                                    place-content: center;
                                    font-weight: bold;
                                }

                                .btn1 {
                                    position: absolute;
                                    bottom: 7px;
                                    right: 10px;
                                    background-color: #0075FF;
                                }

                                .btn2 {
                                    position: absolute;
                                    bottom: 7px;
                                    right: 45px;
                                    background-color: #CFE5FF;
                                }

                                .gst {
                                    position: relative;
                                    position: absolute;
                                    z-index: 10;

                                    input {
                                        background-color: white;
                                        cursor: pointer;
                                        padding-left: 10px;
                                        padding-right: 40px;
                                        font-size: 14px;
                                    }

                                    .con {
                                        width: 100%;
                                        background-color: white;
                                        margin-top: 5px;
                                        border-radius: 4px;
                                        box-shadow: 1px 1px 10px #d2d2d2;
                                        padding-bottom: 10px;
                                        padding-top: 5px;

                                        div {
                                            line-height: 35px;
                                            padding-left: 20px;
                                            cursor: pointer;

                                            &:hover {
                                                background-color: #f9fbfc;
                                            }
                                        }
                                    }
                                }

                            }

                        }
                    }

                    .box7 {
                        height: 240px;
                        position: relative;

                        .section {
                            display: grid;
                            grid-template-columns: 1fr 1fr;
                            margin-top: 30px;

                            .form-group {
                                width: 80%;
                                margin: auto;
                                position: relative;

                                input {
                                    background-color: white;
                                }

                                label {
                                    margin-bottom: 10px;
                                    color: #474747;
                                    font-size: 14px;
                                }

                                span {
                                    position: absolute;
                                    right: 15px;
                                    bottom: 10px;
                                    font-size: 13px;
                                    font-weight: 500;
                                }
                            }
                        }

                        .scan {
                            position: absolute;
                            bottom: 20px;
                            right: 5%;
                            text-transform: none;
                            padding: 3px 5px 3px 5px;
                            font-weight: bold;
                            color: white;
                            background-color: #0075FF;
                        }

                    }
                }

                .two {

                    .box2,
                    .box4,
                    .box6,
                    .box8 {
                        width: 100%;
                        background-color: white;
                        border-radius: 10px;
                        box-shadow: 1px 1px 10px #d2d2d2;

                        .heading {
                            margin-top: 20px;
                            height: 60px;
                            padding-top: 15px;
                            padding-left: 30px;
                            font-weight: 500;
                            font-size: 17px;
                            color: #474747;
                            border-bottom: 1px solid #D9D9D9;
                        }


                    }

                    .box2 {
                        height: 500px;

                        .middle {
                            width: 100%;
                            height: 375px;
                            padding-left: 30px;
                            padding-right: 20px;
                            color: #9c9c9c;
                            font-weight: 500;
                            overflow-y: scroll;

                            &::-webkit-scrollbar {
                                display: none;
                            }

                            .form-group {
                                padding-top: 20px;
                                position: relative;

                                input {
                                    border-color: #0075FF;
                                    margin-right: 20px;
                                }

                                #btn {
                                    width: 20px !important;
                                    height: 20px;
                                    position: absolute;
                                    right: 0px;
                                    bottom: 0px;
                                    border-radius: 50%;
                                    display: grid;
                                    place-content: center;
                                    border: none;
                                    background-color: white;
                                    color: #0F55A7;
                                }
                            }
                        }

                        .footer1 {
                            line-height: 60px;
                            font-weight: 500;
                            font-size: 14px;
                            border-top: 1px solid #D9D9D9;
                            text-align: center;
                            color: #0075FF;
                            cursor: pointer;
                        }

                        .addCategory {
                            width: 100%;
                            height: 100vh;
                            background-color: rgb(0, 0, 0, 0.4);
                            position: fixed;
                            top: 0px;
                            left: 0px;
                            z-index: 20;
                            display: grid;
                            place-content: center;

                            .addBox {
                                width: 600px;
                                height: 420px;
                                background-color: white;
                                border-radius: 10px;
                                position: relative;

                                .addHead {
                                    width: 100%;
                                    line-height: 60px;
                                    background-color: #0075FF;
                                    border-radius: 10px 10px 0px 0px;
                                    padding-left: 20px;
                                    font-size: 17px;
                                    font-weight: 500;
                                    color: white;
                                    letter-spacing: .4px;
                                    word-spacing: 4px;
                                    position: relative;

                                    .btn {
                                        position: absolute;
                                        right: 20px;
                                        top: 10px;
                                        color: white;
                                    }
                                }

                                .form-group {
                                    width: 90%;
                                    margin: auto;
                                    margin-top: 5px;

                                    label {
                                        margin-top: 20px;
                                        color: #797979;
                                        margin-bottom: 10px;
                                    }

                                    .but {
                                        position: absolute;
                                        bottom: 20px;
                                        text-transform: none;
                                        background-color: #0075FF;
                                        color: white;
                                        font-weight: bold;
                                        width: 80px;
                                        padding: 3px 5px 3px 5px;
                                    }

                                    .but1 {
                                        right: 30px;
                                    }

                                    .up {
                                        background-color: #01B574;
                                    }

                                    .but2 {
                                        right: 130px;
                                        background-color: #E31A1A;
                                    }
                                }
                            }
                        }
                    }

                    .box4 {
                        padding-bottom: 20px;


                        .war {
                            width: 100%;

                            label {
                                margin-top: 30px;
                                margin-left: 20px;
                                display: block;
                            }

                            .innerWar {
                                display: grid;
                                grid-template-columns: 1fr 1fr;

                                .form-group {
                                    display: block;
                                    position: relative;


                                    span {
                                        position: absolute;
                                        right: 35px;
                                        bottom: 8px;
                                        color: #474747;
                                        font-weight: 500;
                                        font-size: 14px;
                                    }

                                    input {
                                        width: 80%;
                                        display: inline-block;
                                        margin-left: 30px;
                                        margin-top: 25px;
                                        margin-left: 20px;
                                    }

                                    .inp1 {
                                        padding-right: 55px;
                                    }

                                    .inp2 {
                                        padding-right: 70px;
                                    }
                                }
                            }
                        }

                        .foot1 {
                            width: 85%;
                            margin: auto;
                            height: 80px;
                            margin-top: 40px;

                            div {
                                cursor: pointer;
                                color: #0075FF;
                                font-size: 18px;
                                font-weight: 500;
                            }

                            p {
                                font-size: 12px;
                                color: #797979;
                                padding: 10px;
                                padding-left: 30px;
                                padding-right: 30px;
                            }
                        }

                        .warranty {
                            width: 100%;
                            height: 100vh;
                            position: fixed;
                            top: 0px;
                            left: 0px;
                            background-color: rgb(0, 0, 0, 0.5);
                            display: grid;
                            place-content: center;
                            z-index: 20;

                            .con {
                                width: 500px;
                                height: 380px;
                                background-color: white;
                                border-radius: 15px;
                                position: relative;

                                .head {
                                    border-radius: 15px 15px 0px 0px;
                                    background-color: #0075FF;
                                    line-height: 70px;
                                    padding-left: 20px;
                                    color: white;
                                    font-weight: 500;
                                    font-size: 18px;
                                    letter-spacing: .5px;
                                    position: relative;

                                    .btn {
                                        position: absolute;
                                        font-weight: 500;
                                        color: white;
                                        right: 10px;
                                        top: 5px;
                                    }
                                }

                                label {
                                    margin-left: 25px;
                                }

                                .war {
                                    display: grid;
                                    width: 94%;
                                    grid-template-columns: 1fr 20px 1fr;
                                    margin: auto;
                                }

                                .form-group {
                                    color: #696969;
                                    margin-top: 20px;
                                }

                                input {
                                    border-color: #CFE5FF;
                                    margin: auto;
                                    margin-top: 15px;
                                    width: 90%;
                                }
                            }

                            .btn1,
                            .btn2 {
                                width: 100px;
                                text-transform: none;
                                font-weight: bold;
                                position: absolute;
                                bottom: 25px;
                            }

                            .btn2 {
                                right: 37px;
                                background-color: #0075FF;
                                color: white;
                            }

                            .btn1 {
                                right: 157px;
                                color: #0075FF;
                                border-color: #0075FF;
                            }
                        }
                    }

                    .box6 {
                        height: 310px;

                        .form-group {
                            width: 85%;
                            margin: auto;
                            margin-top: 20px;
                        }

                        label {
                            margin-bottom: 10px;
                            font-size: 14px;
                            color: #797979;
                        }
                    }


                    .box8 {
                        padding-bottom: 30px;

                        .form-group {
                            width: 85%;
                            margin: auto;
                            margin-top: 20px;

                            label {
                                margin-bottom: 10px;
                                font-size: 14px;
                                color: #797979;
                            }

                            input {
                                font-size: 14px;
                                text-transform: none;

                                &::placeholder {
                                    color: #afafaf;
                                }
                            }

                            .linkGrid {
                                display: grid;
                                grid-template-columns: 1fr 40px;

                                input {
                                    border-radius: 4px 0px 0px 4px;
                                }

                                .linkBt {
                                    min-width: 0px;
                                    text-transform: none;
                                    height: 100%;
                                    border-radius: 0px 4px 4px 0px;
                                    color: #F7323F;
                                }
                            }
                        }


                        .btnBox {
                            text-align: right;
                            width: 85%;
                            margin: auto;
                            margin-top: 20px;

                            .addBt {
                                text-transform: none;
                                padding: 2px 30px 2px 30px;
                                background-color: #0075FF;
                                color: white;
                                font-weight: bold;
                            }
                        }

                        .videoPopUp {
                            width: 100%;
                            height: 100vh;
                            position: fixed;
                            top: 0px;
                            left: 0px;
                            background-color: rgb(0, 0, 0, 0.5);
                            display: grid;
                            place-content: center;
                            z-index: 20;

                            .videoCon {
                                width: 500px;
                                height: 250px;
                                background-color: white;
                                border-radius: 15px;
                                position: relative;

                                .head {
                                    border-radius: 15px 15px 0px 0px;
                                    background-color: #0075FF;
                                    line-height: 70px;
                                    padding-left: 20px;
                                    color: white;
                                    font-weight: 500;
                                    font-size: 18px;
                                    letter-spacing: .5px;
                                    position: relative;

                                    .btn {
                                        position: absolute;
                                        font-weight: 500;
                                        color: white;
                                        right: 10px;
                                        top: 5px;
                                    }
                                }

                                label {
                                    margin-left: 25px;
                                }


                                .form-group {
                                    color: #696969;
                                    margin-top: 20px;
                                }

                                input {
                                    border-color: #CFE5FF;
                                    margin: auto;
                                    margin-top: 0px;
                                    width: 90%;
                                    text-transform: none;
                                }

                                .btn1,
                                .btn2 {
                                    width: 100px;
                                    text-transform: none;
                                    font-weight: bold;
                                    position: absolute;
                                    bottom: 25px;
                                }

                                .btn2 {
                                    right: 37px;
                                    background-color: #0075FF;
                                    color: white;
                                }

                                .btn1 {
                                    right: 157px;
                                    color: #0075FF;
                                    border-color: #0075FF;
                                }

                            }
                        }
                    }


                }

            }

            .third {
                width: 100%;
                height: 42px;
                margin-top: 30px;
                position: relative;

                .save,
                .cancel {
                    position: absolute;
                    top: 0px;
                    right: 4.5%;
                    min-width: 120px;
                    text-transform: none;
                    font-weight: bold;
                    font-size: 16px;
                    font-size: 14px;
                    padding: 5px 20px 5px 20px;
                }

                .save {
                    background-color: #0075FF;
                    color: white;
                }

                .cancel {
                    right: 20.5%;
                    color: #0075FF;
                    border-color: #0075FF;
                }
            }

        }
    }
}




@media (max-width:1200px) {
    .addproduct {
        width: 1200px;
    }
}

@media (max-height:700px) {
    .addproduct {
        .inner {
            height: 90vh;
        }
    }
}