.newOrder {
    width: 98% !important;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-bottom: 1px solid rgb(217, 217, 217, 0.5);

    .left {
        display: grid;
        grid-template-columns: 160px 1fr;

        .imgSection {
            display: grid;
            place-content: center;

            img {
                width: 130px;
                height: 130px;
            }
        }

        .content {
            padding: 20px;
            padding-left: 0px;

            section {
                padding-right: 30%;
                color: #0075ff;
                font-weight: bold;
            }

            p {
                padding-right: 45%;
                font-size: 12px;
                color: #797979;
                margin-top: 6px;
                margin-bottom: 5px;
            }

            span {
                color: #797979;
                font-weight: 500;
                font-size: 14px;
            }
        }
    }

    .right {

        .data {
            width: 200px !important;
            margin-right: -20px;
            height: 100%;
            margin-left: auto;
            display: grid;
            grid-template-columns: 1fr 1fr;


            .con1,
            .con2 {
                font-size: 12px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding-right: 25px;
                font-weight: 500;
                color: #797979;

                p {
                    margin-top: 5px;
                    margin-bottom: 5px;
                    color: green;
                }
            }

            .con1 {
                text-align: right;

                div {
                    margin-top: 10px;
                    font-size: 14px;
                    font-weight: 500;
                }
            }

            .con2 {
                text-align: left;
                font-size: 14px;

                section {
                    text-decoration: line-through;
                }

                span {
                    font-size: 10px;
                }

                div {
                    margin-top: 5px;
                    font-size: 16px;
                    font-weight: 500;
                }
            }
        }
    }
}