.login {

    width: 100%;
    height: 100vh;
    position: relative;

    .loader{
        width: 100%;
        height: 100%;
        background-color: rgb(246, 250, 252);
        position: absolute;
        top:0px;
        left: 0px;
        display: grid;
        place-content: center;
        img{
            width: 500px;
            height: auto;
        }
    }


    .navbar {
        background-color: #0F55A7 !important;
        padding-top: 0px;
        padding-bottom: 0px;

        .navbar-brand {
            position: relative;
            color: white !important;
            margin-left: 40px;
            font-family: 'Redressed' !important;
            letter-spacing: 1px;
            font-size: 30px;
            padding: 0px !important;

            span{
              position: absolute;
              top:33px;
              left:1px;
              font-size: 11px;
              font-family: 'Roboto';
              letter-spacing: 4.7px;
            }
        }
    
        .nav-link {
            margin-right: 40px;
            color: white !important;
        }

        .nav-item{
            text-align: center;
        }
    }


    .container {
        .contentSection {
            .data {
                width: 60%;
                margin: auto;
                margin-top: 80px;

                h1,
                .detail {
                    text-align: center;
                }

                .detail {
                    font-size: 12px;
                    color: #797979;
                }

                form {
                    margin-top: 50px;

                    .form-group{
                        position: relative;
                        .err{
                            color: red;
                            position: absolute;
                            bottom: -20px;
                            left: 5px;
                            font-size: 12px;
                        }

                        .eye{
                            position: absolute;
                            bottom: 10px;
                            right: 15px;
                            color: #797979;
                            cursor: pointer;
                        }
                    
                    }

                    input {
                        border-radius: 10px;
                        line-height: 30px;
                        margin-bottom: 30px;
                        box-shadow: 0px 4px 10px rgb(0, 0, 0, 10%);
                    }

                    label {
                        font-weight: 600;
                        margin-bottom: 5px;
                    }

                    .form-check{
                        position: relative;
                        font-size: 14px;
                        font-weight: 500;

                        input{
                        border-radius: 4px !important;
                        }

                        span{
                            position: absolute;
                            right: 1px;
                        }
                    }

                    .bt {
                        margin-top: 20px;
                        width: 100%;
                        line-height: 30px;
                        border-radius: 10px;
                        font-size: 14px;
                        font-weight: 500;
                        text-transform: none;
                        font-weight: 600;
                    }

                    .btn1 {
                        background-color: #0F55A7;
                        color: white;
                    }

                    .btn2 {
                        background-color: white;
                        color: black;

                        img {
                            width: 30px;
                            height: auto;
                            margin-right: 10px;
                        }
                    }
                }

                .bottom{
                    text-align: center;
                    font-size: 12px;
                    margin-top: 20px;
                    font-weight: 500;
                    color: #474747;

                    #signup{
                        color: #0F55A7;
                    }
                }

            }
        }

        .imgSection {
            img {
                width: 100%;
                height: auto;
            }
        }
    }

}


@media (max-width:768px) {
    .login {

        .navbar{
            .nav-link {
                margin-right: 0px;
            }
        }
        .container {
            .contentSection {
                .data{
                    width: 95%;
                }
            }

            .imgSection {
                padding: 20px;
                padding-top: 50px;
            }
        }
    }
}