.setting{
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 750px;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;

    .box{
        width: 90% !important;
        margin: auto;
        background-color: white;
        margin-top: 20px;
        border-radius: 20px;
        padding: 20px;
        padding-bottom: 30px;

        h5{
            font-weight: bold;
            color: #0F55A7;
        }

        .mail, .pass{
            width: 90%;
            margin: auto;
            border-top: 1px solid rgb(197, 196, 196);
            border-bottom: 1px solid rgb(197, 196, 196);
            margin-top: 30px;
            line-height: 60px;
            color: #797979;
            padding-left: 30px;
            font-weight: 600;
            font-family: 'Roboto';
            letter-spacing: .5px;
            word-spacing: 4px;
            cursor: pointer;
        }
        .pass{
            margin-top: 0px;
            border-top: none;
        }

        .plan{
            margin-top: 30px;
        }

        .plans{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            margin-top: 25px;

            .minBox{
                width: 80%;
                margin: auto;
                height: 45vh;
                max-height: 340px;
                background-color: #0F55A7;
                border-radius: 10px;
                color: white;
                padding: 10px;
                padding-left: 0px;
                position: relative;

                p{
                    width: 50%;
                    line-height: 40px;
                    text-align: center;
                    margin: auto;
                    font-size: 18px;
                    font-weight: 700;
                    letter-spacing: .5px;
                    border-bottom: 1px solid white;
                }

                ul{
                    margin-top: 30px;
                    
                    li{
                      list-style: none;
                      font-size: 14px;
                      margin-top: 5px;
                      position: relative;
                      padding-left: 30px;
                      margin-left: -15px;

                      #icon{
                        width: 18px !important;
                        height: 18px !important;
                        background-color: white;
                        position: absolute;
                        left: 0px;
                        border-radius: 50%;
                        top:2px;
                        display: grid;
                        place-content: center;
                        color: green;
                        font-weight: bold;
                      }
                    }
                }
                .btnBox{
                     width: 100%;
                     display: grid;
                     place-content: center;
                     position: absolute;
                     bottom: 20px;
                     left: 0px;
                    .btn5{
                      font-size: 13px;
                      text-transform: none;
                      background-color: white;
                      color: green;
                      font-weight: bold;
                    }
                }
            }
        }

    }


    .mail1{
        width: 100%;
        height: 100%;
        position: absolute;
        top:0px;
        left: 0px;
        background-color: rgb(0, 0, 0, 0.4);
        display: grid;
        place-content: center;

        .inner1,.inner2 {
            width: 500px;
            height: 350px;
            background-color: white;
            border-radius: 20px;
            padding: 15px;
            position: relative;
    
            .header4 {
                color: #474747;
                font-weight: 700;
                letter-spacing: .5px;
                padding-left: 50px;
                font-size: 18px;
    
                .btn1 {
                    position: absolute;
                    left: 25px;
                    top: 23px;
                    font-size: 28px;
                    padding: 0px;
                    height: 15px;
                    width: 30px;
                    display: grid;
                    place-content: center;
                    color: #474747;
                }
            }
    
            .info {
                font-size: 12px;
                color: #797979;
            }
    
            .btn2 {
                position: absolute;
                top: 8px;
                right: 15px;
                padding: 0px;
                color: #C6C6C6;
            }
    
            .form-group {
                width: 90%;
                margin: auto;
                margin-top: 40px;
                position: relative;
    
                input {
                    margin-top: 5px;
                    border: 1px solid #C6C6C6;
                    font-size: 14px;
                    border-radius: 8px;
                    line-height: 25px;
    
                    &::placeholder {
                        color: #C6C6C6;
                    }
                }
    
                #code{
                    position: absolute;
                    color: #0075FF;
                    right: 15px;
                    top: 31px;
                    font-size: 14px;
                    font-weight: 500;
                    letter-spacing: .5px;
                    cursor: pointer;
                }
    
                #time{
                    font-size: 10px;
                    color: #0075FF;
                    position: absolute;
                    right: 2px;
                    bottom: -18px;
                    letter-spacing: .3px;
                    display: none;
                }
            }
    
            .btnBox1 {
                margin-top: 100px;
                text-align: center;
    
                .bt1, .bt2 {
                    background-color: #0075FF;
                    color: white;
                    font-weight: bold;
                    text-transform: none;
                    width: 100px;
                    height: 40px;
                }
                .bt2{
                    background-color: #6B8BAF;
                }
            }
    
        }

        .inner2{
            .form-group{
                margin-top: 25px;
            }
            .btnBox1{
                margin-top: 50px;
            }
        }
    
    }

}

@media (max-width:1200px) {
    .setting{
        width: 1200px;
    }
}

@media (max-height:690px) {
    .setting{
        .box{
           .plans .minBox{
              height: 42vh;
           }
        }
    }
}