.factory_page {
    background-color: #0F55A7;
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;

    .factGrid2 {
        width: 100%;
        height: 100vh;

        .factCon {
            width: 95%;
            margin: auto;
            height: 90.3vh;
            max-height: 680px;
            margin-top: 30px;
            background-color: white;
            border-radius: 20px 20px 0px 0px;
            padding: 20px;
            position: relative;


            .heading {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 300px 200px 200px;

                .details {
                    font-size: 25px;
                    font-weight: 600;
                    color: #0F55A7;
                    font-family: Arial, Helvetica, sans-serif;
                    padding-top: 5px;
                    padding-left: 10px;
                }

                .addBtn {
                    padding-top: 10px;
                    text-align: center;

                    .btnCon {
                        background-color: #0F55A7;
                        color: white;
                        text-transform: none;
                        line-height: 35px;
                        font-weight: 600;
                        letter-spacing: 1px;
                        border-radius: 10px;

                        #btn {
                            margin-right: 10px;
                        }
                    }

                }
            }


            .heading2 {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 50px;
                margin-top: 40px;

                div {
                    text-align: left;
                    font-weight: 500;
                    padding-left: 10px;
                }

            }

            .inner {
                width: 100%;
                margin-top: 10px;
                height: 87%;
                border-top: 1px solid #797979;
                overflow-y: scroll;
                max-height: 560px;

                &::-webkit-scrollbar {
                    display: none;
                }


                .factBox {
                    width: 100%;
                    line-height: 50px;
                    display: grid;
                    grid-template-columns: 1fr 50px;
                    border-bottom: 1px solid #d1d0d0;

                    div {
                        text-align: left;
                        font-weight: 500;
                        padding-left: 10px;
                        color: #474747;
                    }

                    .edit {
                        color: #0F55A7;
                        cursor: pointer;
                    }
                }


            }
  

        }
    }
}