.scannerSection{
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.7);
    position: fixed;
    top:0px;
    left: 0px;
    z-index: 20;
    display: grid;
    place-content: center;
    
    .App-section{
        .cancel{
            height: 40px;
            position: relative;

            .btn{
                color: white;
                position: absolute;
                right: 0px;
                padding: 1px 10px 1px 10px;
            }
        }
    }
}