.home {

    .scroll {
        width: 100%;
        height: 76vh;
        padding-bottom: 10px;
        max-height: 640px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

    }

    .bottom {
        .box1 {

            section {
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
                padding-left: 25px;
                padding-top: 15px;
            }

            .head {
                width: 80%;
                margin: auto;
                display: grid;
                grid-template-columns: 1fr 1fr;
                margin-top: 20px;

                .div1,
                .div2 {
                    color: #0075FF;
                    font-weight: 600;
                    font-size: 18px;
                }

                .div2 {
                    text-align: right;
                }
            }

            .inner2 {
                margin: auto;
                margin-top: 10px;
                width: 90%;
                height: 445px;
                border-top: 1px solid rgb(204, 203, 203);
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }

                .main {
                    width: 100%;
                    margin: auto;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    line-height: 50px;
                    border-bottom: 1px solid rgb(204, 203, 203);


                    .d1,
                    .d2 {
                        color: #797979;
                        font-weight: 600;
                        margin-left: 29px;
                    }

                    .d2 {
                        font-weight: normal;
                        text-align: right;
                        margin-right: 27px;
                        font-size: 13px;

                        span {
                            color: green;
                            font-size: 10px;
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }

}


@media (max-width:1200px) {
    .home{
        width: 1200px;
    }
}
@media (max-height:710px) {
    .home{
        .scroll {
            height: 74vh;
        }
    
    }
}
@media (max-height:670px) {
    .home{
        .scroll {
            height: 73vh;
        }
    
    }
}

