.cate{
    width: 95%;
    border-radius: 15px;
    margin: auto;
    height: auto;
    background-color:rgb(207, 229, 255, 0.25);
    margin-top: 25px;
    margin-bottom: 10px;
    box-shadow: 1px 1px 10px rgb(237, 237, 237);
    padding: 20px;
    padding-left: 15px;
    padding-right: 15px;
    z-index: 1;
    cursor: pointer;
    position: relative;

    .heading{
        font-size: 20px;
        color: #0F55A7;
        font-weight: bold;
        font-family: Arial, Helvetica, sans-serif;
        padding-top: 5px;
        padding-left: 10px;
        margin-bottom: 20px;
    }

    .sendbutton{
        position: absolute;
        top:10px;
        right:20px;
        min-width: 30px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        color: white;
    }
}


.cateBox{
    display: inline-block;
    width: 23%;
    margin-Top: 10px;
    border-radius: 10px;
    background-color: white;
    margin-left: 10px;
    margin-right: 10px;
    padding-bottom: 10px;
    cursor: pointer;

    .imgBoc{
        width: 100%;
        height: 220px;
        background-color: white;
        border-radius: 10px 10px 0px 0px;
    }

    img{
        width: 100%;
        height: 220px;
        border-radius: 10px 10px 0px 0px;
    }
    h5{
        text-align: right;
        color: #474747;
        padding-right: 5px;
        padding-top: 5px;
    }
    p{
        font-size: 14px;
        font-weight: 500;
        padding: 5px;
        padding-top: 0px;
    }
    .f1{
        font-size: 10px;
        margin-top: -10px;
        padding-left: 5px;
        padding-right: 5px;
    }
    section{
        margin-top: 5px;
        font-size: 12px;
        padding: 5px;
        font-weight: 500;
        color: #6B8BAF;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}