.history {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 820px;
    display: grid;
    grid-template-columns: 220px 1fr;


    .historyCon {
        width: 95%;
        height: 91vh;
        margin: auto;
        background-color: white;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 680px;

        .inner {
            width: 98%;
            height: 98% !important;
            position: relative;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 300px;
                margin: auto;
                margin-top: -50px;
                text-align: center;
                display: flex;
                justify-content: space-around;
                color: #6B8BAF;
                font-size: 14px;
                font-weight: 600;

                span {
                    padding-right: 26px;
                    border-right: 2px solid #D9D9D9;
                    cursor: pointer;

                    &:hover {
                        color: #3a5e86;
                    }
                }

                .activeLink {
                    color: #0F55A7;
                }
            }

            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top: 0px;


                input {
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }

                span {
                    position: absolute;
                    top: 3px;
                    right: 10px;
                    color: white;
                }

                div {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }

            .head {
                width: 100%;
                line-height: 40px;
                margin-top: 25px;
                display: grid;
                grid-template-columns: 1fr 1fr 160px 160px;
                color: #0F55A7;

                div {
                    font-weight: 500;
                }

                .d1 {
                    padding-left: 5px;
                }

                .d2 {
                    padding-left: 15%;
                }

                .d3,
                .d4 {
                    text-align: center;
                }
            }


            .approved {
                .productBox {
                    width: 100%;
                    height: 72vh;
                    max-height: 560px;
                    margin-top: 10px;
                    overflow-y: scroll;
                    border-top: 1px solid rgb(227, 226, 226);


                    &::-webkit-scrollbar {
                        display: none;
                    }
                }
            }



        }
    }
}

///////////////////////////////////////////////////////////////////////////


@media (max-width:1200px) {
    .history{
         width: 1200px !important;
    }
}

@media (max-height:720px) {
    .history{
        .historyCon{
            height: 90.5vh;
        }
    }
}

@media (max-height:685px) {
    .history{
        .historyCon{
            height: 90vh;
            .inner{
                .approved{
                    .productBox{
                        height: 70vh;
                    }
                }
            }
        }
    }
}