.AddDealer {
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: rgb(0, 0, 0, 0.5);
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .form {
        width: 750px;
        height: 540px;
        background-color: white;
        border-radius: 15px;
        padding: 10px;
        position: relative;

        .headDealer {
            color: #0F55A7;
            font-size: 18px;
            font-weight: 700;
            padding-left: 20px;
            padding-top: 5px;

            .btn {
                position: absolute;
                top: 5px;
                right: 10px;
                font-weight: 500;
                box-shadow: none;
                color: #C6C6C6;
            }
        }

        .divide {
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;

            .first,
            .second {

                .form-group {
                    width: 85%;
                    margin: auto;
                    margin-top: 20px;
                    position: relative;

                    label {
                        margin-bottom: 5px;
                        font-size: 15px;
                        font-weight: 500;
                        color: #797979;
                    }

                    input {
                        line-height: 20px;
                        color: #797979;

                        &::placeholder {
                            color: #C6C6C6;
                        }
                    }

                    .errors {
                        color: red;
                        font-size: 10px;
                        position: absolute;
                        left: 0px;
                        bottom: -17px;
                    }


                    .select1 {
                        width: 80px;
                        position: absolute;
                        top: 0px;
                        left: 0px;

                        label {
                            cursor: pointer;
                            position: relative;

                            span {
                                position: absolute;
                                top: -6px;
                            }
                        }

                        div {
                            padding: 5px;
                            padding-bottom: 7px;
                            background-color: white;
                            border-radius: 5px;
                            margin-top: -1px;
                            box-shadow: 1px 1px 5px #CFE5FF;

                            .bt2 {
                                margin-top: 2px;
                                width: 70px;
                                font-weight: 600;
                                color: #469afa;
                                border-color: #CFE5FF;
                                height: 30px;
                                font-size: 13px;
                                text-transform: none;
                            }
                        }
                    }

                }
            }
        }

        .btn1 {
            position: absolute;
            bottom: 20px;
            right: 40px;
            text-transform: none;
            font-weight: bold;
            color: white;
            background-color: #0075FF;
        }

        .btn2 {
            position: absolute;
            bottom: 20px;
            right: 40px;
            text-transform: none;
            font-weight: bold;
            color: white;
            background-color: #0075FF;
        }

        .btn3 {
            position: absolute;
            bottom: 20px;
            right: 180px;
            text-transform: none;
            font-weight: bold;
            color: white;
            background-color: #E31A1A;
        }
    }
}