.animationss{
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: rgb(250, 250, 250);
    position: absolute;
    top:0px;
    left:0px;
    z-index: 50;
    display: grid;
    place-content: center;
    overflow: hidden;

    .mainCon{
        width: 600px;
        position: relative;
        text-align: center;

    .imgCon{
        width: 100%;
        height: 400px;
        display: grid;
        place-content: center;
        overflow: hidden;

        img{
            width: 800px;
            height: auto;
        }
    }
    .text1{
        color: #0F55A7;
        text-align: center;
        margin-top: 100px;
        margin-bottom: 100px;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: .7px;
        transition: 1s;
        opacity: 0;
    }
    #but1{
        background-color: #0463d0;
        text-transform: none;
        font-weight: 600;
        width: 150px;
        height: 40px;
        color: white;
        font-size: 16px;
        opacity: 0;
        transition: 3s;
    }
}

}