.backCover {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    background-color: rgb(0, 0, 0, 0.4);
    z-index: 20;
    display: grid;
    place-content: center;

    .filterBox {
        width: 500px;
        padding-bottom: 20px;
        background-color: white;
        border-radius: 20px;
        box-shadow: 1px 1px 10px #d2d2d2;

        .filterHeading {
            line-height: 50px;
            padding-left: 25px;
            font-weight: 500;
            font-size: 17px;
            border-bottom: 1px solid #D9D9D9;
            background-color: #0075FF;
            border-radius: 20px 20px 0px 0px;
            color: white;
            position: relative;
            margin-bottom: 25px;

            .btn {
                position: absolute;
                top: 3px;
                right: 10px;
                color: #D9D9D9;
                padding: 5px;
                height: auto;
                box-shadow: none;
            }
        }

        .boxGrid,
        .boxGrid1,
        .boxGrid2 {
            width: 85%;
            margin: auto;
            display: grid;
            grid-template-columns: 65px 1fr;
            margin-top: 10px;

            section {
                font-size: 14px;
                color: #797979;
                font-weight: 600;
            }

            input {
                line-height: 18px;
                font-size: 13px;
                border-radius: 6px;
                font-weight: 500;

                &::placeholder {
                    color: #c6c6c6;
                }
            }

        }

        .boxGrid1 {
            margin-bottom: 20px;

            input {
                width: 70%;
                box-shadow: none;
                background-color: white;
            }
        }

        .boxGrid2 {
            margin-top: 25px;
            cursor: pointer;

            .filter1 {
                width: 100%;
                line-height: 27px;
                font-size: 14px;
                border-radius: 6px;
                border: 1px solid #c6c6c6;
                padding-left: 10px;
                color: #c6c6c6;
                font-weight: 500;
                text-transform: capitalize;
            }

            #filterColor{
                color: #474747;
            }

            .innerGrid {
                display: grid;
                grid-template-columns: 1fr 5px 1fr;

                .selectBox {
                    position: relative;

                    .innerSelect {
                        position: absolute;
                        width: 100%;
                        z-index: 2;
                        border-radius: 6px;
                        top: 0px;
                        left: 0px;
                        border: 1px solid #C6C6C6;
                        background-color: white;

                        section {
                            background-color: #CFE5FF;
                            color: #0075FF;
                            font-size: 12px;
                            line-height: 30px;
                            padding-left: 10px;
                        }

                        div {
                            font-size: 12px;
                            color: #797979;
                            font-weight: 500;
                            padding-left: 15px;
                            line-height: 28.5px;
                            cursor: pointer;

                            &:hover {
                                background-color: #e9ebeb;
                            }
                        }
                    }
                }
            }
        }


        .btBox {
            text-align: right;
            width: 85%;
            margin: auto;
            margin-top: 25px;

            .bt1,
            .bt2 {
                text-transform: none;
                padding: 2px 25px 2px 25px;
                background-color: #0075FF;
                color: white;
                font-weight: bold;
            }

            .bt1 {
                margin-right: 25px;
            }
        }


    }
}