.multipleLogo {
    background-color: #0F55A7;
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;


    .logoInner {
        width: 100%;
        height: 100vh;


        .updateLogo {
            position: fixed;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100vh;
            background-color: rgba(0, 0, 0, 0.4);
            display: grid;
            place-content: center;
            z-index: 10;

            .updateForm {
                width: 500px;
                background-color: white;
                padding: 20px;
                padding-top: 30px;
                position: relative;

                .clear {
                    position: absolute;
                    top: 6px;
                    right: 6px;
                    min-width: 20px;
                    padding: 2px;
                }

                label {
                    font-weight: 600;
                    margin-bottom: 5px;
                }

                input {
                    margin-bottom: 20px;
                }

                .logoBt {
                    background-color: #0F55A7;
                    color: white;
                    font-weight: bold;
                    text-transform: none;
                    width: 150px;
                    margin-top: 10px;
                }
            }
        }


        .addForm {
            position: relative;
            top: 20px;
            width: 85%;
            margin: auto;
            background-color: white;
            height: 110px;
            border-radius: 10px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;

            .form-group {
                width: 85%;
                padding-top: 18px;
                padding-left: 25px;

                label {
                    font-weight: 600;
                    margin-bottom: 5px;
                }
            }

            .addBtBox {
                display: grid;
                place-content: center;

                .addBt {
                    text-transform: none;
                    width: 150px;
                    height: 40px;
                    background-color: #0F55A7;
                    color: white;
                    font-weight: bold;
                }
            }
        }

        .logoList {
            margin: auto;
            width: 85%;
            margin-top: 50px;
            height: 66vh;
            background-color: white;
            border-radius: 10px;
            overflow-y: scroll;

            &::-webkit-scrollbar {
                display: none;
            }

            .logoBox {
                width: 95%;
                margin: auto;
                display: grid;
                grid-template-columns: 100px 1fr 1fr 1fr;
                padding: 10px;
                border-bottom: 1px dotted black;

                section,
                .count {
                    font-weight: 600;
                    font-size: 18px;
                    padding-top: 20px;
                    padding-left: 20px;
                }

                .imgLogoCon {
                    img {
                        width: 150px;
                        height: 80px;
                    }
                }

                .logoBtnBox {
                    padding-top: 20px;

                    .bt1 {
                        margin-left: 50px;
                        color: white;
                        background-color: green;
                        font-weight: bold;
                    }

                    .bt2 {
                        margin-left: 20px;
                        color: white;
                        background-color: red;
                        font-weight: bold;
                    }
                }
            }

        }

    }

}