.profile {
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: #0F55A7;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;


    .personal {
        width: 90%;
        height: 38vh;
        background-color: white;
        margin: auto;
        margin-top: 20px;
        border-radius: 20px;
        position: relative;

        .multiLogoBtn {
            background-color: #0F55A7;
            position: absolute;
            top: 10px;
            right: 100px;
            color: white;
            font-weight: bold;
            z-index: 10;
        }

        .head {
            width: 100%;
            padding-left: 30px;
            line-height: 50px;
            padding-top: 0px !important;
            font-weight: 600;
            color: #0F55A7;
            font-size: 20px;
            position: relative;

            .btn {
                position: absolute;
                right: 35px;
                top: 10px;
                color: #0F55A7;
            }
        }

        .data {
            width: 100%;
            display: grid;
            grid-template-columns: 250px 1fr;

            .logo {
                width: 100%;
                height: 90%;

                .logoImg {
                    width: 150px;
                    height: 150px;
                    margin-top: 40px;
                    margin-left: 40px;
                    position: relative;
                    background-image: url('../../assests/logo.png');
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;

                    .imageContainer {
                        width: 100%;
                        height: 100%;
                        background-color: white;
                    }

                    #coverImg {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0px;
                        background-color: black;
                        opacity: 0;
                        transition: .8s;
                        border-radius: 5px;
                    }

                    #btBox {
                        position: absolute;
                        top: 55px;
                        left: 53px;
                        z-index: 2;
                        display: none;

                        .imgbt {
                            min-width: 0px;
                            padding: 6px 12px 6px 12px;
                            background-color: white;
                            overflow: hidden;

                            #file {
                                top: -40px;
                                left: 0px;
                                width: 100%;
                                height: 80px;
                                position: absolute;
                                cursor: pointer;
                                opacity: 0;
                            }
                        }
                    }


                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 5px;
                    }

                    &:hover {
                        #coverImg {
                            opacity: 0.7;
                        }

                        #btBox {
                            display: block;
                        }
                    }

                }

            }

            .content {
                padding-top: 22px;
                height: 90%;

                span {
                    font-size: 14px;
                    color: #797979;
                    font-weight: 500;
                }

                div {
                    width: 94%;
                    line-height: 40px;
                    border: 1px solid rgb(204, 202, 202);
                    border-radius: 5px;
                    padding-left: 20px;
                    color: #666666;
                    font-weight: 600;
                    margin-top: 10px;
                    margin-bottom: 20px;
                    min-height: 40px;
                }
            }
        }
    }

    .contact {
        width: 90%;
        height: 48vh;
        background-color: white;
        margin: auto;
        margin-top: 10px;
        border-radius: 20px;

        .head {
            width: 100%;
            padding-left: 30px;
            line-height: 50px;
            padding-top: 0px !important;
            font-weight: 600;
            color: #0F55A7;
            font-size: 20px;
        }

        .con {
            padding-left: 40px;
            margin-top: 6px;
        }

        span {
            font-size: 14px;
            color: #797979;
            font-weight: 500;
        }

        p {
            width: 95%;
            line-height: 40px;
            border: 1px solid rgb(204, 202, 202);
            border-radius: 5px;
            color: #666666;
            font-weight: 600;
            margin-top: 5px;
            margin-bottom: 20px;
            padding-left: 20px;
        }
    }

    .edit {
        width: 100%;
        height: 100vh;
        max-height: 850px;
        position: fixed;
        top: 0px;
        left: 0px;
        background-color: rgb(0, 0, 0, 0.5);
        display: grid;
        place-content: center;

        .dataCon {
            width: 740px;
            height: 580px;
            background-color: white;
            border-radius: 20px;
            padding: 30px;

            .form-group {
                position: relative;
            }

            div {
                font-weight: 500;
                font-size: 18px;
                width: 100%;
                position: relative;

                .btn {
                    position: absolute;
                    right: 0px;
                    top: -20px;
                    color: #0F55A7;
                }
            }

            .error {
                color: red;
                font-size: 10px;
                position: absolute;
                left: 0px;
                bottom: -15px;
                font-weight: 400;
            }

            label {
                font-size: 14px;
                margin-top: 15px;
                margin-bottom: 5px;
                color: #797979;
            }

            section {
                width: 95px;
                margin-left: auto;
                margin-top: 20px;

                .btn {
                    background-color: #0F55A7;
                    color: white;
                    padding: 2px 30px 3px 30px;
                }
            }
        }
    }
}

@media (max-width:1200px) {
    .profile {
        width: 1200px;
    }
}

@media (max-height:680px) {
    .profile {
        .personal {
            .data {
                .content {
                    padding-top: 8px;

                    div {
                        margin-top: 5px;
                        margin-bottom: 15px;
                    }
                }

                .logo {
                    img {
                        margin-top: 20px;
                    }
                }
            }
        }

        .contact {
            .con {
                margin-top: -6px;

                p {
                    margin-bottom: 15px;
                }
            }
        }
    }
}