.distribute {
    width: 100%;
    height: 100vh;
    max-height: 800px;
    background-color: #0F55A7;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;

    .part1 {
        background-color: white;
    }

    .part2 {
        width: 100%;

        .distCon {
            width: 95%;
            margin: auto;
            height: 90.3vh;
            max-height: 680px;
            margin-top: 30px;
            background-color: white;
            border-radius: 20px 20px 0px 0px;
            padding: 20px;
            position: relative;

            .heading {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 300px 200px 200px;

                .details {
                    font-size: 25px;
                    font-weight: 600;
                    color: #0F55A7;
                    font-family: Arial, Helvetica, sans-serif;
                    padding-top: 5px;
                    padding-left: 10px;
                }

                .addBtn {
                    padding-top: 10px;
                    text-align: center;

                    .btnCon {
                        background-color: #0F55A7;
                        color: white;
                        text-transform: none;
                        line-height: 35px;
                        font-weight: 600;
                        letter-spacing: 1px;
                        border-radius: 10px;

                        #btn {
                            margin-right: 10px;
                        }
                    }

                }

                .s1,
                .s2 {
                    padding-top: 18px;
                    text-align: right;
                    padding-right: 10px;
                    position: relative;

                    .bt {
                        background-color: #0F55A7;
                        color: white;
                        border: none;
                        line-height: 20px;
                        width: 150px;
                        border-radius: 5px;
                        text-transform: none;
                        font-weight: 600;
                        letter-spacing: 1px;
                        font-size: 12px;
                    }

                    .arr {
                        position: absolute;
                        right: 12px;
                    }

                    div {
                        width: 150px;
                        background-color: white;
                        margin-left: auto;
                        position: relative;
                        z-index: 10;
                        margin-top: 5px;
                        border-radius: 10px;
                        padding: 5px;
                        padding-top: 0px;
                        box-shadow: 1px 1px 5px #c3dcfa;

                        .but {
                            width: 100%;
                            margin-top: 5px;
                            text-transform: none;
                            font-size: 10px;
                            font-weight: 600;
                            border-color: #CFE5FF;
                            color: #0075FF;
                        }
                    }
                }
            }

            .heading2 {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 200px 200px 200px 130px;
                margin-top: 40px;

                div {
                    text-align: center;
                    font-weight: 500;
                    font-size: 14px;
                }

                #p1 {
                    text-align: left !important;
                    padding-left: 10px;
                }
            }

            .inner {
                width: 100%;
                margin-top: 10px;
                height: 87%;
                border-top: 1px solid #797979;
                overflow-y: scroll;
                max-height: 560px;

                &::-webkit-scrollbar {
                    display: none;
                }

                .distributerBox {
                    width: 100%;
                    display: grid;
                    grid-template-columns: 1fr 200px 200px 200px 130px;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    border-bottom: 1px solid #d1d0d0;

                    div {
                        text-align: center;
                        font-weight: 500;
                        font-size: 14px;
                    }

                    #p2 {
                        text-align: left !important;
                        padding-left: 10px;

                        section {
                            color: #0075FF;
                            font-size: 18px;
                        }

                        span {
                            color: #797979;
                            font-size: 12px;
                        }
                    }

                    #p3 {

                        section {
                            color: #0075FF;
                            font-size: 18px;
                        }

                        span {
                            color: #797979;
                            font-size: 12px;
                        }
                    }

                    #p4 {
                        color: #797979;
                    }

                    #p5 {
                        color: #01B574;
                    }

                    #p6 {
                        color: #0F55A7;

                        span {
                            cursor: pointer;
                        }
                    }
                }
            }

            //////////////////////////////////////////////////////////////////////////////////////

            .popUp {
                width: 100%;
                height: 100%;
                background-color: rgb(0, 0, 0, 0.3);
                position: absolute;
                top: 0px;
                left: 0px;
                border-radius: 20px 20px 0px 0px;
                display: grid;
                place-content: center;

                .cent {
                    width: 310px;
                    background-color: white;
                    border-radius: 10px;
                    padding: 10px 20px 10px 20px;

                    h5 {
                        text-align: center;
                        position: relative;

                        .btn {
                            position: absolute;
                            right: -10px;
                            top: 0px;
                            color: #C6C6C6;
                            height: 20px;
                            width: 20px;
                            display: grid;
                            place-content: center;
                        }
                    }

                    table {
                        color: #909090;
                        width: 100%;
                        font-size: 13px;
                        font-weight: 500;

                        tr {
                            td {
                                min-width: 60px;
                                padding-top: 5px;
                            }

                            #t1 {
                                min-width: 20px;
                            }
                        }
                    }

                    .bttBox {
                        text-align: center;
                        margin-top: 10px;

                        .bnt {
                            text-transform: none;
                            height: 20px;
                            background-color: #CFE5FF;
                            color: #0F55A7;
                            font-weight: 600;
                            font-size: 10px;
                            width: 100px;
                        }
                    }
                }
            }

        }
    }
}

////////////////////////////////////////////////////////////////////////////////

@media (max-width:1200px) {
    .distribute {
        width: 1200px;
    }
}

@media (max-height:720px) {
    .distribute {
        .part2 {
            .distCon {
                height: 89.8vh;

                .inner {
                    height: 86%;
                }
            }
        }
    }
}
@media (max-height:680px) {
    .distribute {
        .part2 {
            .distCon {
                height: 89.3vh;

                .inner {
                    height: 85%;
                }
            }
        }
    }
}
@media (max-height:655px) {
    .distribute {
        .part2 {
            .distCon {
                height: 89vh;
            }
        }
    }
}
