.purchase {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 800px;
    display: grid;
    grid-template-columns: 220px 1fr;


    .purCon {
        width: 95%;
        height: 91vh;
        margin: auto;
        background-color: white;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 720px;


        .inner {
            width: 98%;
            height: 99%;
            position: relative;

            .detail {
                font-size: 25px;
                font-weight: 600;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }


            .category,
            .sorting {
                width: 140px;
                height: 200px;
                z-index: 10;


                input {
                    width: 140px;
                    border-radius: 5px;
                    line-height: 30px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }

                span {
                    position: absolute;
                    top: 1px;
                    right: 10px;
                    color: white;
                }

                div {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }

            .category {
                margin-left: auto;
                margin-top: -45px;
                position: relative;
                margin-right: 310px;
            }

            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 152px;
                top: 8px;
            }

            .cardItems {
                position: absolute;
                top: 8px;
                right: 10px;
                width: 125px;
                height: 29px;
                background-color: #0F55A7;
                color: white;
                text-transform: none;
                font-weight: 600;
                font-size: 12px;

                span {
                    margin-left: 10px;
                }
            }

            .purchaseBox {
                width: 100%;
                height: 93%;
                max-height: 650px;
                position: absolute;
                top: 45px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }
}

////////////////////////////////////////////////////////////////////////////

@media (max-width:1200px) {
    .purchase {
        width: 1200px;
    }
}

@media (max-height:710px) {
    .purchase {
        .purCon {
            height: 90vh;
        }
    }
}

@media (max-height:650px) {
    .purchase {
        .purCon {
            height: 89.5vh;
            .inner{
                .purchaseBox{
                    height: 92%;
                }
            }
        }
    }
}