.factory {
    width: 100%;
    height: 330px;
    background-color: white;
    border-radius: 10px;
    margin-top: 25px;

    .factHead {
        position: relative;

        section {
            font-size: 20px;
            font-weight: bold;
            color: #0F55A7;
            padding-left: 25px;
            padding-top: 15px;
        }

        .btnAdd {
            position: absolute;
            top: 17px;
            right: 22px;
            width: 130px;
            text-transform: none;
            background-color: #0F55A7;
            color: white;
            font-weight: bold;
        }
    }

    .head2 {
        width: 90%;
        line-height: 40px;
        margin: auto;
        margin-top: 10px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        font-weight: 500;
        font-size: 14px;
        color: #0F55A7;
        border-bottom: 1px solid #E9E9E9;


        .d {
            text-align: right;
        }
    }

    .inner_scroll {
        width: 90%;
        margin: auto;
        height: 225px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        .factBox {
            padding: 10px 0px 10px 0px;
            border-bottom: 1px solid #E9E9E9;
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            font-size: 14px;
            color: #474747;
            font-weight: 500;

            .d {
                text-align: right;

                .btn {
                    color: #0F55A7;
                    padding: 0px;
                    box-shadow: none;
                }
            }
        }
    }

}


