.addFact {
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.4);
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .inner {
        width: 420px;
        height: auto;
        min-height: 200px;
        background-color: white;
        border-radius: 20px;
        padding: 25px;
        padding-top: 15px;
        position: relative;


        .head {
            color: #0F55A7;
            font-weight: bold;
            font-size: 20px;
            letter-spacing: .3px;
        }

        .head2 {
            font-weight: 700;
            margin-top: 16px;
            margin-bottom: -5px;
            letter-spacing: .4px;
            color: #474747;
        }

        .btn {
            position: absolute;
            top: 10px;
            right: 20px;
            padding: 0px;
            color: #C6C6C6;
            box-shadow: none;
        }

        .b1 {
            position: absolute;
            top: 50px;
            color: #0F55A7;
        }


        form {

            .d2 {
                width: 90%;
                margin: auto;

                .form-group {
                    margin-top: 15px;
                    font-weight: 500;
                    color: #797979;
                    font-size: 14px;

                    input {
                        margin-top: 5px;
                        font-size: 12px;
                        border: 1px solid rgba(0, 0, 0, 0.25);
                        border-radius: 8px;
                        line-height: 22px;
                        letter-spacing: .5px;

                        &::placeholder {
                            color: #C6C6C6;
                        }
                    }

                    .info {
                        font-size: 11px;
                        letter-spacing: .5px;
                        text-align: center;
                        font-style: italic;
                        color: #797979;
                        font-weight: 400;
                        padding-left: 20px;
                    }
                }


            }

            .btnCon {
                text-align: center;
                margin-top: 20px;

                .next {
                    text-transform: none;
                    width: 100px;
                    height: 35px;
                    color: white;
                    font-weight: bold;
                    background-color: #0075FF;
                }

                .save {
                    margin-top: 10px;
                }

            }

            #btnEdit {
                position: relative;
                margin-top: 20px;
                text-align: right;

                .next {
                    // position: absolute;
                    // right: 17px;
                    margin-right: 20px;
                    text-transform: none;
                    width: 100px;
                    height: 30px;
                    color: white;
                    font-weight: bold;
                    background-color: #0075FF;
                }

                .delete {
                    // position: absolute;
                    // right: 130px;
                    margin-right: 10px;
                    text-transform: none;
                    width: 100px;
                    height: 30px;
                    color: white;
                    font-weight: bold;
                    background-color: #E31A1A;
                }
            }
        }


        form {
            .d3 {
                width: 90%;
                margin: auto;
                height: 320px;
                margin-bottom: -5px;
                position: relative;

                .uppertext {
                    font-size: 10px;
                    font-style: italic;
                    color: #797979;
                    font-weight: 500;
                    margin-top: 20px;
                }

                .inp1 {
                    width: 100%;
                    background-color: #eaf1fc;
                    line-height: 20px;
                    margin-top: 10px;
                    font-size: 11px;
                    font-weight: 500;
                    border-radius: 8px;
                    padding-left: 40px;
                    letter-spacing: .5px;

                    &::placeholder {
                        color: #a4a4a4;
                    }

                }

                .icon1 {
                    position: absolute;
                    top: 33px;
                    left: 10px;
                    font-size: 20px;
                    color: #a4a4a4;
                }

                .icon2 {
                    position: absolute;
                    top: 73px;
                    left: 10px;
                    font-size: 25px;
                    z-index: 2;
                    color: #a4a4a4;
                }

                .inp2 {
                    width: 100%;
                    line-height: 20px;
                    margin-top: 10px;
                    font-size: 11px;
                    font-weight: 500;
                    background-color: white;
                    border: 1px solid rgba(0, 0, 0, 0.25);
                    border-radius: 8px;
                    padding-left: 40px;
                    letter-spacing: .4px;
                    cursor: pointer;

                    &::placeholder {
                        color: #a4a4a4;
                    }

                    box-shadow: none;
                }

                .boxCon {
                    width: 100%;
                    margin-top: 10px;
                    height: 210px;
                    border-radius: 8px;
                    border: 1px solid #CFE5FF;
                    overflow-y: scroll;

                    .productBox {
                        width: 90%;
                        margin: auto;
                        height: 50px;
                        position: relative;
                        margin-top: 5px;
                        margin-bottom: 5px;
                        color: #797979;

                        img {
                            position: absolute;
                            width: 40px;
                            height: 40px;
                            left: 5px;
                            top: 5px;
                        }

                        .name {
                            position: absolute;
                            left: 60px;
                            top: 5px;
                            width: 160px;
                            font-size: 14px;
                            font-weight: 500;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .spcate {
                            position: absolute;
                            width: 170px;
                            left: 60px;
                            font-size: 12px;
                            bottom: 7px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .btn {
                            position: absolute;
                            right: 5px !important;
                            top: 15px;
                            padding: 0px;
                            width: 20px;
                            height: 20px;
                            min-height: 10px;
                            display: grid;
                            place-content: center;
                            color: #797979;
                        }

                    }
                }


                .boxPop {
                    position: absolute;
                    width: 335px;
                    height: 220px;
                    bottom: -12px;
                    left: 0px;
                    border-radius: 8px;
                    background-color: white;
                    box-shadow: 0px 0px 5px #a4a4a4;

                    .top1 {
                        width: 100%;
                        line-height: 40px;
                        border-bottom: 1px solid #D9D9D9;
                        color: #949393;
                        font-weight: 600;
                        font-size: 14px;
                        text-align: right;
                        padding-right: 50px;
                        letter-spacing: .3px;
                        font-family: Arial, Helvetica, sans-serif;

                        input {
                            position: relative;
                            top: 8px;
                            left: 8px;
                            width: 17px;
                            height: 17px;
                            border-color: #0075FF;
                        }
                    }

                    .inner1 {
                        height: 178px;
                        padding-top: 5px;
                        overflow-y: scroll;
                    }

                    .productBox {
                        width: 90%;
                        margin: auto;
                        height: 50px;
                        position: relative;
                        margin-bottom: 5px;
                        color: #797979;

                        img {
                            position: absolute;
                            width: 40px;
                            height: 40px;
                            left: 5px;
                            top: 5px;
                        }

                        .name {
                            position: absolute;
                            left: 60px;
                            top: 5px;
                            width: 160px;
                            font-size: 14px;
                            font-weight: 500;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .spcate {
                            position: absolute;
                            width: 170px;
                            left: 60px;
                            font-size: 12px;
                            bottom: 7px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .form-check-input {
                            position: absolute;
                            right: 9px;
                            top: 9px;
                            width: 17px;
                            height: 17px;
                            border-color: #0075FF;
                        }

                    }
                }

            }

        }

    }

}