.heading {
  width: 100%;
  height: 40px;
  margin-top: -15px;

  ul {
    margin-right: 40px;
    color: white;
    font-size: small;

    li {
      float: right;
      margin-left: 20px;
      list-style: none;
      margin-top: 10px;
      position: relative;

      .link1 {
        text-decoration: none;

        &:hover {
          color: white;
        }
      }

      span {
        position: absolute;
        left: 5px;
        top: 1px;
        color: #0f55a7;
      }

      input {
        width: 160px;
        border-radius: 8px;
        border: none;
        margin-top: 1px;
        font-size: 12px;
        padding: 2px;
        text-align: center;
      }
    }
  }
}