.productDetails {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 760px;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;


    .itemCon1 {
        width: 95%;
        height: 91vh;
        margin: auto;
        background-color: white;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 690px;

        .inner1 {
            width: 98%;
            height: 99%;
            max-height: 655px;
            display: grid;
            grid-template-columns: 1fr 1fr;

            .imgSectionBox {
                max-width: 42vw;
                min-width: 500px;

                .boxContainer {
                    margin: auto;
                    margin-top: 20px;
                    width: 90%;
                    height: 80%;
                    border-radius: 10px;
                    border: 1px dotted #C6C6C6;
                    display: flex;
                    flex-direction: column;

                    .imgBox {
                        width: 300px;
                        margin: auto;
                        padding-top: 30px;

                        img {
                            width: 300px;
                            max-height: 280px;
                        }
                    }

                    .selector {
                        width: 90%;
                        margin-left: auto;
                        margin-right: auto;
                        margin-top: auto;
                        margin-bottom: 25px;

                        overflow-x: scroll;
                        white-space: nowrap;

                        &::-webkit-scrollbar {
                            display: none;
                        }

                        div {
                            border-radius: 5px;
                            border: 1px dotted #C6C6C6;
                            margin-left: 5px;
                            margin-right: 5px;
                            display: inline-block;
                            cursor: pointer;

                            img {
                                width: 77px;
                                height: 77px;
                                border-radius: 5px;
                            }
                        }
                    }


                }

            }

            .contentSection {
                padding-left: 10px;
                position: relative;

                h2 {
                    margin-top: 12px;
                }

                .top {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    margin-top: 20px;

                    .con1 {
                        .d1 {
                            color: #797979;
                            font-weight: 500;
                            margin-bottom: 20px;
                        }

                        span {
                            color: #797979;
                            font-weight: 600;
                        }

                        section {
                            color: #6B8BAF;
                            font-weight: 600;
                            margin-top: 20px;
                        }

                        .p1 {
                            margin-top: 15px;
                            font-weight: 600;
                            color: #797979;

                            div {
                                display: inline-block;
                                font-size: 22px;
                                margin-left: 10px;
                                color: #0075FF;
                            }
                        }

                        .p2 {
                            margin-top: 15px;
                            font-weight: 600;
                            color: #797979;

                            div {
                                display: inline-block;
                                font-size: 18px;
                                margin-left: 10px;
                                font-weight: 700;
                            }
                        }
                    }

                    .con2 {
                        div {
                            width: 200px;
                            height: 250px;
                            background-color: #CFE5FF;
                            border-radius: 10px;
                            margin: auto;
                            margin-top: -10px;

                            .p2 {
                                width: 100px;
                                line-height: 30px;
                                font-size: 14px;
                                margin: auto;
                                text-align: center;
                                border-bottom: 1px solid #474747;
                                font-weight: 500;
                                color: #474747;
                            }

                            .p3 {
                                font-size: 10px;
                                margin-top: 10px;
                                padding-left: 10px;
                                padding-right: 10px;
                                height: 198px;
                                overflow: hidden;
                                text-overflow: ellipsis;
                            }
                        }
                    }
                }

                .describe {
                    margin-top: 10px;
                    color: #797979;
                    font-size: 14px;
                    padding-right: 50px;

                    p {
                        font-weight: bold;
                        font-size: 16px;
                        margin-bottom: 10px;
                    }
                }

            }
        }
    }
}


@media (max-width:1200px) {
    .productDetails {
        width: 1200px;
    }
}

@media (max-height:720px) {
    .item1 {
        .itemCon {
            height: 90.5vh;
        }
    }
}

@media (max-height:690px) {
    .item1 {
        .itemCon {
            height: 90vh;
        }
    }
}

@media (max-height:650px) {
    .item1 {
        .itemCon {
            height: 89.5vh;
        }
    }
}