.cityCon{
    width: 85%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 15px;
    font-size: 12px;
    line-height: 30px;
    border-top: 1px solid rgb(176, 176, 176);
    font-weight: 500;
    color: #797979;

    .data{
        padding-left: 7px;
        font-weight: 400;

        span{
         margin-left: 10px;
         font-size: 10px;
         color: green;
        }
    }

}
