.historyCard{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 70%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid rgb(227, 226, 226);
    padding-left: 10px;

    .name{
        padding-top: 10px;

        div{
            font-size: 23px;
            font-weight: 600 !important;
            margin-top: 10px;
        }
        #d1{
            color: #01B574;
        }
        #d2{
            color: #E31A1A;
        }
        #d3{
            color: #FFB800;
        }

        .text{
            color: #C6C6C6;
            font-size: 12px;
            font-weight: 500;
            margin-top: 10px;
            margin-bottom: 5px;
        }
        .date{
            color: #797979;
            font-size: 14px;
            margin-top: 5px;
            font-weight: 500;
        }
        .amt{
            font-weight: 500;
            color: #797979;
            font-size: 18px;
        }
    }

    .itemBox{
        .item{
            width: 100%;
            padding: 10px;
            display: grid;
            grid-template-columns: 1fr 150px 150px ;
            color: #797979;

            .main{
                display: grid;
                grid-template-columns: 90px 1fr;
                img{
                    width: 60px;
                    height: 60px;
                }
                p{
                    font-size: 12px;
                    margin-bottom: 0px;
                    max-width: 200px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }

            .m{
                 display: grid;
                 place-content: center;
            }
        }
    }
}