.products {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 820px;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;


    .ProCon {
        width: 95%;
        height: 91vh;
        margin: auto;
        background-color: white;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 680px;

        .inner {
            width: 98%;
            height: 99%;
            position: relative;

            .detail {
                font-size: 25px;
                font-weight: 600;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 180px;
                height: 50px;
                margin: auto;
                margin-top: -55px;
                margin-bottom: 10px;

                .btn {
                    background-color: #0F55A7 !important;
                    color: white;
                    text-transform: none;
                    line-height: 35px;
                    border-radius: 10px;
                    font-weight: 600;
                    letter-spacing: .7px;

                    span {
                        margin-left: 4px;
                    }
                }
            }


            .category,
            .sorting {
                width: 140px;

                .sort,
                .categ {
                    position: relative;

                    .ss1 {
                        position: absolute;
                        z-index: 2;
                        right: 15px;
                        top: 0px;
                        color: white;
                    }
                }


                .sbtn {
                    width: 140px;
                    height: 30px;
                    background-color: #0F55A7;
                    text-transform: none;
                    font-weight: 600;
                    color: white;
                    font-size: 12px;
                    letter-spacing: 1px;
                    border-radius: 5px;

                }


                .btnCon1,
                .btnCon2 {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;
                    position: relative;
                    z-index: 2;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }

            .category {
                position: absolute;
                right: 180px;
                top: 0px;
            }

            .sorting {
                width: 140px;
                position: absolute;
                right: 10px;
                top: 0px;
            }

            .head {
                width: 100%;
                line-height: 40px;
                display: grid;
                grid-template-columns: 1fr repeat(6, 130px);

                div {
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                }
            }

            .productBox {
                width: 100%;
                height: 84%;
                max-height: 550px;
                overflow-y: scroll;
                border-top: 1px solid rgb(178, 177, 177);

                &::-webkit-scrollbar {
                    display: none;
                }
            }

        }
    }
}


///////////////////////////////////////////////////////////////

@media (max-width:1330px) {
    .products {
        .ProCon {
            .inner {
                .head {
                    grid-template-columns: 1fr 130px 130px 100px 130px 80px 80px !important;
                }

            }

        }
    }
}

@media (max-width:1200px) {
    .products {
        width: 1200px;
    }
}

@media (max-height:720px) {
    .products {
        .ProCon {
            height: 90.5vh;

            .inner {
                .productBox {
                    height: 83%;
                }
            }
        }
    }
}

@media (max-height:675px) {
    .products {
        .ProCon {
            height: 90vh;

            .inner {
                .productBox {
                    height: 82%;
                }
            }
        }
    }
}

@media (max-height:645px) {
    .products {
        .ProCon {
            height: 89.5vh;

            .inner {
                .productBox {
                    height: 81%;
                }
            }
        }
    }
}
