.pro {
    width: 100%;
    height: 100px;
    border-top: 1px solid rgb(178, 177, 177);
    display: grid;
    grid-template-columns: 1fr 130px 130px 130px 130px 130px 130px;
    color: #0075FF;
    cursor: pointer;

    .div1 {
        position: relative;

        img {
            width: 70px;
            height: 70px;
            margin-top: 15px;
            margin-left: 20px;
            border-radius: 10px;
            border: 1px solid #0075FF;
        }

        p {
            position: absolute;
            top: 28px;
            left: 40%;
            font-weight: 600;
        }

        span {
            position: absolute;
            top: 55px;
            left: 40%;
            font-size: 10px;
        }
    }

    .d {
        display: grid;
        place-content: center;
        font-size: 13px;
        font-weight: 500;

        span {
            position: relative;
            cursor: pointer;
        }

        #box {
            border-radius: 5px;
            position: relative;
            text-align: center;
            
            .icon{
                position: relative;
                z-index: 1;
            }
            
            #big {
                position: absolute;
                top:-10px;
                left: -89px;
                display: none;
                z-index: 2;

                .img1{
                    width: 200px;
                    height: auto;
                }
                .img2{
                    width: 150px;
                    height: 150px;
                    position: absolute;
                    bottom: 45px;
                    left: 25px;
                }
                .bt1{
                    width: 80px;
                    height: 20px;
                    background-color: #CFE5FF;
                    text-transform: none;
                    position: absolute;
                    bottom: 20px;
                    left: 60px;
                    font-size: 10px;
                    font-weight: 600;
                    color: #0075FF;
                }
            }
            
            &:hover{
                
                #big{
                    display: block;
                }
            }
        }
    }
}

/////////////////////////////////////////////////////////////////////////////////////////

@media (max-width:1330px) {
    .pro {
        grid-template-columns: 1fr 130px 130px 100px 130px 80px 80px !important;
    }
}

@media (max-width:1200px) {
    .pro {
        .div1 {

            p,
            span {
                left: 45% !important;
            }
        }
    }
}