.acquired {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 800px;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;


    .acquiredCon {
        min-width: 95%;
        width: 80vw;
        height: 91vh;
        margin: auto;
        background-color: white;
        max-height: 700px;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;


        .inner {
            width: 98%;
            height: 98%;
            position: relative;
            max-height: 670px;

            form {
                width: 100%;
                height: 100%;
            }

            .detail {
                font-size: 20px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 0px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .serachBox {
                width: 100%;
                position: absolute;
                top: -8px;
                left: 0px;
                display: flex;
                justify-content: center;

                div {
                    position: relative;

                    input {
                        width: 350px;
                        line-height: 25px;
                        border-radius: 10px;
                        border-color: #c6c6c6;
                        font-size: 13px;
                        color: #474747;
                        font-weight: 500;

                        &::placeholder {
                            color: #c6c6c6;
                        }
                    }

                    .searchBt {
                        position: absolute;
                        right: 5px;
                        top: 5px;
                        height: 30px;
                        min-width: 0px;
                        width: 30px;
                        border-radius: 50%;
                        color: #797979;
                        border: none;
                    }
                }


            }


            .export {
                width: 130px;
                height: 40px;
                position: absolute;
                right: 10px;
                top: -10px;
                border-radius: 10px;
                background-color: #0F55A7;
                color: white;
                text-transform: none;
                font-weight: bold;
                letter-spacing: .8px;

                .tune {
                    margin-right: 8px;
                }
            }

            .filter {
                width: 130px;
                height: 40px;
                position: absolute;
                right: 160px;
                top: -10px;
                border-radius: 10px;
                background-color: #0F55A7;
                color: white;
                text-transform: none;
                font-weight: bold;
                letter-spacing: .8px;

                .tune {
                    margin-right: 8px;
                }
            }


            .tableMain {
                height: 93%;
                overflow: scroll;
                border-top: 1px solid black;

                // &::-webkit-scrollbar {
                //     display: none;
                // }

                td {
                    min-width: 200px;
                }
            }


            .tableHead,
            .tableBody {
                width: 1620px;
                padding-top: 10px;
                display: grid;
                grid-template-columns: 120px 150px 200px 120px 150px 120px 120px 100px 150px 85px 100px 200px;
                font-size: 14px;
                font-weight: 600;
                padding-bottom: 15px;
                border-bottom: 1px solid #56577A;
                color: black;

                div {
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                    padding-right: 10px;

                }
            }


            .tableBody {
                border: none;
                padding: 0px;
                line-height: 60px;
                font-weight: 500;
                color: #797979;

                a {
                    color: #797979;
                }
            }

        }
    }

}


@media (max-height:720px) {
    .acquired .acquiredCon {
        height: 89.5vh;
    }
}

@media (max-height:670px) {
    .acquired .acquiredCon {
        .inner .tableMain {
            height: 92%;
        }
    }
}

@media (max-height:620px) {
    .acquired .acquiredCon {
        height: 89vh;

        .inner .tableMain {
            height: 91%;
        }
    }
}

@media (max-width:1200px) {
    .acquired {
        width: 1200px;
    }
}