:root{
    --anim1 : 435;
    --anim2 : 435
}

.circleChart {

    display: grid;
    place-content: center;
    margin-top: 18px;
    
    .circleGraph {
        position: relative;
        width: 145px;
        height: 145px;
        border-radius: 50%;
        display: grid;
        place-content: center;
        background-color: #CFE5FF;
    }
    
    .inner {
        width: 120px;
        height: 120px;
        background-color: #F6FAFF;
        border-radius: 50%;
        display: grid;
        place-content: center;
        color: #0075FF;
        font-size: 18px;
        font-weight: 500;
        text-align: center;
        
        span {
            display: block;
            color: #797979;
            font-size: 14px;
            position: relative;
            top: -3px;
        }
    }
    
    #ring {
        fill: none;
        stroke: url("#GradientColor");
        stroke-width: 12px;
        stroke-dasharray: 435;
        stroke-dashoffset: 435;
        animation: anim 1.5s linear forwards;
    }
    
    #ring2 {
        fill: none;
        stroke: url("#GradientColor");
        stroke-width: 12px;
        stroke-dasharray: 435;
        stroke-dashoffset: 435;
        animation: anim2 1.5s linear forwards;
    }
    
    svg {
        position: absolute;
        top: -10.75px;
        left: -2.6px;
        transform: rotate(269deg);
    }
    
}

@keyframes anim {
    100% {
        stroke-dashoffset: var(--anim1);
    }
}

@keyframes anim2 {
    100% {
        stroke-dashoffset: var(--anim2);
    }
}

