.order,
.newPage {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 820px;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;

    .orderCon {
        width: 95% !important;
        height: 91vh;
        margin-left: 0px !important;
        background-color: white;
        margin-right: 0px !important;
        margin: auto !important;
        margin-top: 25px !important;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 680px;

        .inner {
            width: 98%;
            height: 99% !important;
            position: relative;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 300px;
                margin: auto;
                margin-top: -50px;
                text-align: center;
                display: flex;
                justify-content: space-around;
                color: #6B8BAF;
                font-size: 14px;
                font-weight: 600;

                span {
                    padding-right: 26px;
                    border-right: 2px solid #D9D9D9;
                    cursor: pointer;

                    &:hover {
                        color: #3a5e86;
                    }
                }

                .activeLink {
                    color: #0F55A7;
                }
            }

            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top: 0px;


                input {
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }

                .sortBox {
                    position: relative;

                    .b1 {
                        width: 140px;
                        background-color: #0F55A7;
                        height: 30px;
                        text-transform: none;
                        font-weight: 600;
                        color: white;
                        letter-spacing: .7px;
                    }
                    .sp1 {
                        position: absolute;
                        top: 0px;
                        right: 15px;
                        color: white;
                    }
                }


                .btnCon3 {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }

            .approved {
                .head {
                    width: 100%;
                    line-height: 40px;
                    margin-top: 25px;
                    display: grid;
                    grid-template-columns: 1fr 1fr 90px 200px !important;
                    color: #0F55A7;

                    div {
                        font-weight: 500;
                    }

                    .d1 {
                        padding-left: 5px;
                    }

                    .d2 {
                        padding-left: 15%;
                    }

                    .d3,
                    .d4 {
                        text-align: center;
                    }
                }

                .productBox {
                    width: 100%;
                    height: 73vh !important;
                    max-height: 560px;
                    margin-top: 10px;
                    overflow-y: scroll;
                    border-top: 1px solid rgb(227, 226, 226);


                    &::-webkit-scrollbar {
                        display: none;
                    }
                }
            }



            .footer {
                position: absolute;
                bottom: 0px;
                right: 0px;

                div {
                    width: 100%;
                    height: 50px;

                    .btn1,
                    .btn2 {
                        padding: 5px 30px 5px 30px;
                        color: white;
                        font-weight: 700;
                        text-transform: none;
                        width: 140px;
                    }

                    .btn1 {
                        background-color: #01B574;
                    }

                    .btn2 {
                        background-color: #E31A1A;
                        margin-right: 20px;
                    }
                }
            }


        }
    }
}


//////////////////////////////////////////////////////////////////////////////////

.newRequestCon {
    width: 100%;
    height: 80vh;
    max-height: 575px;
    overflow-y: scroll;
    margin-top: 30px;

    &::-webkit-scrollbar {
        display: none;
    }
}


.new {

    .orderDetail {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 10px;
        border-bottom: 1px solid gray;

        .left {
            padding-right: 50px;

            .head {
                color: #0F55A7;
                font-weight: 500;
            }

            .data {
                margin-left: 20px;

                div {
                    font-size: 22px;
                    font-weight: 600;
                    color: #0075FF;
                }

                span,
                p {
                    font-size: 12px !important;
                    color: #797979;
                    margin-top: 5px;
                }
            }
        }

        .right {

            text-align: right !important;

            .head {
                color: #0F55A7;
                width: 100px;
                margin-left: auto;
                font-weight: 500;
            }

            .data {
                padding-right: 20px;

                div {
                    color: #01B574;
                    font-weight: 600;
                    font-size: 20px;
                    text-align: right;
                }

                span,
                p {
                    font-size: 14px;
                    color: #0F55A7;
                    font-weight: 500;
                    text-align: right;
                }

                span {
                    padding-left: 0% !important;
                }

                p {
                    font-size: 20px;
                    font-weight: 500;
                }
            }
        }
    }

    .head2 {
        width: 100%;
        height: 40px;
        margin-top: 10px;
        margin-bottom: -20px;
        font-size: 18px;
        color: #0F55A7;
        font-weight: 500;
        position: relative;

        p {
            position: absolute;
            left: 30px;
            top: 3px
        }

        div {
            position: absolute;
            right: 30px;
            top: 3px
        }
    }

    .orderContainer {
        margin-top: 10px;
        width: 100%;
        height: 53vh;
        overflow-y: scroll;
        overflow-x: hidden;

        &::-webkit-scrollbar {
            display: none;
        }
    }

}


//////////////////////////////////////////////////////////////////////////////

@media (max-width:1350px) {
    .order {
        height: auto !important;

        .orderCon {
            margin-right: 20px !important;
        }
    }
}


@media (max-width:1200px) {

    .order,
    .newPage {
        width: 1200px !important;
    }
}

@media (max-height:735px) {

    .order,
    .newPage {
        .orderCon {
            height: 90vh !important;

            .inner {
                .approved {
                    .productBox {
                        height: 72vh !important;
                    }
                }
            }
        }
    }

    .new {
        .orderContainer {
            height: 50vh !important;
        }
    }
}

@media (max-height:700px) {
    .new {
        .orderContainer {
            height: 48vh !important;
        }
    }
}

@media (max-height:680px) {

    .order,
    .newPage {
        .orderCon {
            height: 90vh !important;

            .inner {
                .approved {
                    .productBox {
                        height: 71vh !important;
                    }
                }
            }
        }
    }
}

@media (max-height:660px) {

    .order,
    .newPage {
        .orderCon {
            height: 89vh !important;

            .inner {
                .approved {
                    .productBox {
                        height: 69vh !important;
                    }
                }
            }
        }
    }

    .new {
        .orderContainer {
            height: 45vh !important;
        }
    }
}