.factoryOperator {
    background-color: #0F55A7;
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;

    .factOptGrid2 {
        width: 100%;
        height: 100vh;


        .operatorCon {
            width: 95%;
            margin: auto;
            height: 90.3vh;
            max-height: 680px;
            margin-top: 30px;
            background-color: white;
            border-radius: 20px 20px 0px 0px;
            padding: 20px;
            position: relative;


            .heading {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 300px 200px 200px;

                .details {
                    font-size: 25px;
                    font-weight: 600;
                    color: #0F55A7;
                    font-family: Arial, Helvetica, sans-serif;
                    padding-top: 5px;
                    padding-left: 10px;
                }

                .addBtn {
                    padding-top: 10px;
                    text-align: center;

                    .btnCon {
                        background-color: #0F55A7;
                        color: white;
                        text-transform: none;
                        line-height: 35px;
                        font-weight: 600;
                        letter-spacing: 1px;
                        border-radius: 10px;

                        #btn {
                            margin-right: 10px;
                        }
                    }

                }
            }

            .heading2 {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 1fr 50px;
                margin-top: 40px;

                div {
                    text-align: left;
                    font-weight: 500;
                    padding-left: 10px;
                }

            }

            .inner {
                width: 100%;
                margin-top: 10px;
                height: 87%;
                border-top: 1px solid #797979;
                overflow-y: scroll;
                max-height: 560px;

                &::-webkit-scrollbar {
                    display: none;
                }

                .factBox {
                    padding: 7px 0px 7px 0px;
                    border-bottom: 1px solid #E9E9E9;
                    width: 100%;
                    display: grid;
                    grid-template-columns: 1fr 1fr 50px;
                    color: #797979;
                    font-weight: 500;

                    .d1 {
                        color: #474747;
                        padding-left: 10px;

                        span {
                            font-size: 12px;
                            position: relative;
                            top: -2px;
                            color: #878686;
                            letter-spacing: .4px;
                        }
                    }

                    .d2 {
                        font-size: 13px;
                        display: grid;
                        align-items: center;
                        padding-left: 10px;
                    }

                    .d3 {
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .btn {
                            padding: 0px;
                            box-shadow: none;
                            color: #0F55A7;
                        }
                    }
                }


            }

        }

    }
}