.carousel {
    width: 400px;
    height: 250px;

    .carousel-inner {

        img {
            width: 400px;
            height: 250px;
        }

        .bBox{
            text-align: center;
            position: absolute;
            right: 6px;
            top: 5px;
            z-index: 10;
            .btn{
                border-radius: 50%;
                text-align: center;
                background-color: rgb(255, 0, 0, 0.5);
                width: 25px;
                height: 25px;
                display: grid;
                place-content: center;
                color: rgb(255, 255, 255);
            }
        }
    }
}