.querry{
    margin: auto;
    width: 95%;
    padding: 10px;
    margin-top: 20px;

    .ques, .reply{
      width: 95%;
      height: 200px;
      background: linear-gradient(white,white,#f5f3f3);
      margin-left: auto;
      border-radius: 10px;
      padding: 20px;
      padding-top: 10px;
      position: relative;

      .name{
        font-size: 18px;
        font-weight: 500;
        margin-bottom: -5px;
      }
      span{
        font-size: 12px;
        font-weight: 500;
        color: #797979;
      }
      p{
        font-size: 14px;
        margin-top: 15px;
        color: #797979;
      }

      .circle{
        width: 35px;
        height: 35px;
        background-color: #D9D9D9;
        border-radius: 50%;
        position: absolute;
        top: 10px;
        left: -50px;
      }
    }

    .options{
      margin-left: auto;
        width: 95%;
        line-height: 25px;
        display: grid;
        grid-template-columns: 120px 120px 170px 220px 1fr;
        font-size: 13px;
        font-weight: 500;
        margin-top: 5px;
        color: #C6C6C6;

        div{
        text-align: center;
        border-left: 2px solid #C6C6C6;
        }
    }

    .reply{
        width: 80%;
        margin-top: 10px;
        height: 180px;

        .circle{
            width: 25px;
            height: 25px;
            top:15px;
            left: -30px;
        }
    }
}