.non {
    text-decoration: none;
    cursor: pointer;
}

.active {
    .Items {
        background-color: #CFE5FF !important;

        .imgCon {
            background-color: #0075FF !important;
            color: white !important;
        }
    }
}

.sideBar {
    width: 220px;
    height: 100%;
    background-color: white;
    padding-top: 40px;
    color: #0F55A7;
    font-weight: 600;
    position: absolute;
    left: 0px;
    top: 0px;
    overflow-y: scroll;
    padding-bottom: 20px;
    &::-webkit-scrollbar{
        display: none;
    }

    h2 {
        text-align: center;
        font-family: 'Redressed';
        position: relative;
        margin-bottom: 40px;

        span {
            position: absolute;
            top: 33px;
            font-size: 12px;
            font-family: 'Roboto';
            letter-spacing: 3.7px;
            left: 67px;
        }
    }

    .Items {
        width: 190px;
        line-height: 45px;
        margin: auto;
        margin-top: 10px;
        border-radius: 15px;
        position: relative;
        padding-left: 50px;
        color: #0075FF;
        font-size: 14px;

        .imgCon {
            width: 30px;
            height: 30px;
            background-color: #CFE5FF;
            position: absolute;
            top: 8px;
            left: 10px;
            border-radius: none;
            border-radius: 8px;
            display: grid;
            place-content: center;
        }
    }

    .Items:hover {
        background-color: #CFE5FF;

        .imgCon {
            background-color: #0075FF;
            color: white;
        }
    }

    section {
        margin-top: 30px;
        margin-left: 30px;
        margin-bottom: 15px;
    }
}