.gateCart{
    width: 93%;
    height: 260px;
    border-radius: 20px;
    display: inline-block;
    border: 1px solid #CFE5FF;
    background-color: #F6FAFF;
    position: relative;

    .header{
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        margin-top: 15px;
        color: #474747;
    }

    // @keyframes anim {
    //     100% {
    //         stroke-dashoffset: 160;
    //     }
    // }

    .val1{
        position: absolute;
        bottom: 10px;
        left: 20px;
        letter-spacing: .5px;
        font-weight: 500;

        div{
            font-size: 12px;
            color: #797979;
            text-align: left;
        }
        span{
            color: #01B574;
            position: relative;
            top:-3px;
            font-size: 14px;
            left: -1px;
        }
        #sp1{
            color: #01B574;
        }
        #sp2{
            color: #E31A1A;
        }
        #sp3{
            color: #FADD3B;
        }
    }
    .val2{
        position: absolute;
        bottom: 10px;
        right: 20px;
        letter-spacing: .5px;
        font-weight: 500;
        div{
            font-size: 12px;
            color: #797979;
            text-align: center;
        }
        span{
            color: #0075FF;
            position: relative;
            top:-3px;
            font-size: 14px;
        }
    }

}