.pro {
    width: 100%;
    height: 100px;
    border-top: 1px solid rgb(178, 177, 177);
    display: grid;
    grid-template-columns: 1fr repeat(6, 130px);
    color: #0075FF;

    .div1{
        position: relative;

        .imgContainer{
            width: 70px;
            height: 70px;
            background-color: white;
        }

        img{
            width: 70px;
            height: 70px;
            margin-top: 15px;
            margin-left: 20px;
            border-radius: 10px;
            border: 1px solid #0075FF;
        }

        p{
            position: absolute;
            top:28px;
            left: 40%;
            // font-size: 14px;
            font-weight: 600;
        }
        span{
            position: absolute;
            top:55px;
            left: 40%;
            font-size: 10px;
        }
    }

    .d{
        display: grid;
        place-content: center;
        font-size: 13px;
        font-weight: 500;

        img{
            width: 20px;
            height: 20px;
        }
    }
}