.activeQuerry {
    color: #1f344b !important;
}

.querries {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 800px;
    position: relative;
    display: grid;
    grid-template-columns: 220px 1fr;

    .querryCon {
        width: 95%;
        height: 91vh;
        margin: auto;
        background-color: white;
        max-height: 700px;
        margin-top: 25px;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;

        .inner {
            width: 98%;
            height: 98%;
            position: relative;
            max-height: 670px;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 300px;
                margin: auto;
                margin-top: -50px;
                text-align: center;
                display: flex;
                justify-content: space-around;
                color: #6B8BAF;
                font-size: 14px;
                font-weight: 600;

                span {
                    padding-right: 26px;
                    border-right: 2px solid #D9D9D9;
                    cursor: pointer;

                    &:hover {
                        color: #3a5e86;
                    }
                }

                .activeLink {
                    color: #0F55A7;
                }
            }

            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top: 0px;
                z-index: 2;


                input {
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    cursor: pointer;
                    outline: none;
                }
                
                .sortQ {
                    .but {
                        color: white;
                        background-color: #0F55A7 !important;
                        width: 140px;
                        height: 35px;
                        text-transform: none;
                        font-weight: 600;
                        letter-spacing: .7px;
                    }
                }

                .s1 {
                    position: absolute;
                    top: 3px;
                    right: 10px;
                    color: white;
                }

                .btnCon4 {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }
        }

    }
}

.unsolved,
.solved,
.deniedQuery {
    width: 100%;
    height: 93%;
    max-height: 625px;
    margin-top: 20px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        display: none;
    }
}

.unsolved {
    padding-top: 10px;
}

.solved {
    background-color: rgb(176, 227, 203);
}

.deniedQuery {
    background-color: rgb(175, 175, 231);
}


@media (max-width:1200px) {
    .querries {
        width: 1200px;
    }
}

@media (max-height:720px) {
    .querries {
        .querryCon {
            height: 90.5vh;
        }
    }
}

@media (max-height:690px) {
    .querries {
        .querryCon {
            height: 90vh;
        }
    }
}