.gate_keeper {
    width: 100%;
    height: 330px;
    background-color: white;
    border-radius: 10px;
    margin-top: 25px;

    .gateHead {
        position: relative;

        section {
            font-size: 20px;
            font-weight: bold;
            color: #0F55A7;
            padding-left: 25px;
            padding-top: 15px;
        }

        .btnAdd {
            position: absolute;
            top: 17px;
            right: 22px;
            width: 130px;
            text-transform: none;
            background-color: #0F55A7;
            color: white;
            font-weight: bold;
        }
    }

    .head2 {
        width: 90%;
        line-height: 40px;
        margin: auto;
        margin-top: 10px;
        display: grid;
        grid-template-columns: 1fr 1fr 50px !important;
        font-weight: 500;
        font-size: 14px;
        color: #0F55A7;
        border-bottom: 1px solid #E9E9E9;

        .d {
            text-align: right;
        }
    }

    .inner_scroll {
        width: 90%;
        margin: auto;
        height: 225px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        .gate_Box {
            padding: 7px 0px 7px 0px;
            border-bottom: 1px solid #E9E9E9;
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 50px;
            font-size: 14px;
            color: #797979;
            font-weight: 500;

            .d1 {
                color: #474747;

                span {
                    font-size: 10px;
                    position: relative;
                    top: -2px;
                    color: #878686;
                    letter-spacing: .4px;
                }
            }

            .d2 {
                font-size: 13px;
                display: grid;
                align-items: center;
            }

            .d3 {
                display: flex;
                align-items: center;
                justify-content: end;

                .btn {
                    padding: 0px;
                    box-shadow: none;
                    color: #0F55A7;
                }
            }
        }
    }
}



////////////////////////////////////////////////////////////////////////////////////

.addGate {
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.4);
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .inner {
        width: 420px;
        min-height: 500px;
        background-color: white;
        border-radius: 20px;
        padding: 25px;
        padding-top: 15px;
        position: relative;


        .head {
            color: #0F55A7;
            font-weight: bold;
            font-size: 20px;
            letter-spacing: .3px;
        }

        .head2 {
            font-weight: 700;
            margin-top: 16px;
            margin-bottom: -5px;
            letter-spacing: .4px;
            color: #474747;
        }

        .btn {
            position: absolute;
            top: 10px;
            right: 20px;
            padding: 0px;
            color: #C6C6C6;
            box-shadow: none;
        }

        .b1 {
            position: absolute;
            top: 50px;
            color: #0F55A7;
        }

        form {

            .d2 {
                width: 90%;
                margin: auto;

                .form-group {
                    margin-top: 15px;
                    font-weight: 500;
                    color: #797979;
                    font-size: 14px;

                    input {
                        margin-top: 5px;
                        font-size: 12px;
                        border: 1px solid rgba(0, 0, 0, 0.25);
                        border-radius: 8px;
                        line-height: 22px;
                        letter-spacing: .5px;

                        &::placeholder {
                            color: #C6C6C6;
                        }
                    }

                    .info {
                        font-size: 11px;
                        letter-spacing: .5px;
                        text-align: center;
                        font-style: italic;
                        color: #797979;
                        font-weight: 400;
                        padding-left: 20px;
                    }


                    .form-check{
                        margin-top: 5px;
                        font-weight: 400;
                        font-size: 12px;
                    }

                }


            }

            .btnCon {
                text-align: center;
                margin-top: 20px;

                .save {
                    text-transform: none;
                    width: 100px;
                    height: 35px;
                    color: white;
                    font-weight: bold;
                    background-color: #0075FF;
                }
            }

            .btnEdit {
                position: relative;
                margin-top: 10px;
                height: 30px;

                .save {
                    position: absolute;
                    right: 17px;
                    text-transform: none;
                    width: 100px;
                    height: 30px;
                    color: white;
                    font-weight: bold;
                    background-color: #0075FF;
                }

                .delete {
                    position: absolute;
                    right: 130px;
                    text-transform: none;
                    width: 100px;
                    height: 30px;
                    color: white;
                    font-weight: bold;
                    background-color: #E31A1A;
                }
            }
        }

    }

}



.gateListCon {
    position: absolute;
    top: 198px;
    left: 43px;
    width: 334px;
    height: 200px;
    background-color: white;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    font-size: 12px;

    input {
        border: none;
        width: 100%;
        border-radius: 8px;
        line-height: 33px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
        padding-left: 10px;
        outline: none;
    }

    .addText {
        line-height: 33px;
        padding-left: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
        cursor: pointer;
    }

    .searchResults {
        width: 100%;
        height: 129px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }
    }
}